@import url('https://fonts.googleapis.com/css2?family=Plus+Jakarta+Sans:ital,wght@0,200..800;1,200..800&display=swap');

#ecosystemsAlliances{

    margin:0 auto;
    padding: 100px 0px 0px 100px;
    background-color: #101010;
}

#ecosystemsAlliances .ecosystemsMain {

   background-color: #101010;

}

#ecosystemsAlliances .ecosystemsMain .ecosystemsTitleboxs{

    padding: 180px 290px 50px;
    margin:0 auto;
    overflow: hidden;
}

#ecosystemsAlliances .ecosystemsMain .ecosystemsTitleboxs h5{

    font-size: 7.5rem;
    font-weight: 500;
    color:#fff;
    font-family: "Plus Jakarta Sans", sans-serif;
    opacity: 0;
    transform: translateY(100px);
}

#ecosystemsAlliances .ecosystemsMain .ecosystemsTitleboxs h5.active{

    transition: 800ms;
    transform: translateY(0px);
    opacity: 1;
}

#ecosystemsAlliances .ecosystemsMain .ecosystemsTitleboxs h5 p{

   padding:0px 0px 0px 200px;
   opacity:0;
}

#ecosystemsAlliances .ecosystemsMain .ecosystemsTitleboxs h5.active p{

   opacity: 1;
   transition: 100ms;
}

#ecosystemsAlliances .ecosystemsMain .ecosystemsTitleboxs .subTextBoxs{

  padding-top:80px;
  opacity: 0;
  transform: translateY(100px);

}

#ecosystemsAlliances .ecosystemsMain .ecosystemsTitleboxs h5.active ~ .subTextBoxs{

   opacity: 1;
   transform: translateY(0px);
   transition: 1s;
}
#ecosystemsAlliances .ecosystemsMain .ecosystemsTitleboxs .subTextBoxs p{

   font-size: 26px;
   font-weight: 600;
   color:#fff;
   padding-bottom:20px;
}

/* english 버전 추가 */
#ecosystemsAlliances .ecosystemsMain .ecosystemsTitleboxs .subTextBoxs .enSubFirstTexts{
   
   line-height: 36px;
}

/**/


#ecosystemsAlliances .ecosystemsMain .ecosystemsTitleboxs .subTextBoxs span{
 
    font-size: 18px;
    font-weight: 400;
    line-height: 32px;
    color:#fff;
    display: block;
    width:calc(100% / 2.2);
    word-break: keep-all;
    opacity: 60%;
}

/* english 버전 추가 */

#ecosystemsAlliances .ecosystemsMain .enEcosystemsTitleboxs .subTextBoxs span{
   width: 100%;
}
