#ecosystemsAlliances .ecosystemsMain .ecosystemsContents{

   width: calc(100% - 150px);
   height:780px;
   margin:0 auto;
   border-top:1px solid #343434;
   margin-top:50px;
   display: flex;
   align-items: center;
   justify-content: center;
   background-position: center center;
   background-repeat: no-repeat;
   background-size: 115%;

}

#ecosystemsAlliances .ecosystemsMain .ecosystemsContents .ecosysCompaniesWrap{

    height:100%;
    margin:0 auto;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    justify-content: center;

}

#ecosystemsAlliances .ecosystemsMain .ecosystemsContents .ecosysCompaniesWrap .companiesContent{

   display: flex;
   align-items: center;
   justify-content: flex-end;
   padding: 34px 0px;

}

#ecosystemsAlliances .ecosystemsMain .ecosystemsContents .ecosysCompaniesWrap .companiesContent:last-child{

   padding:0px;
}


#ecosystemsAlliances .ecosystemsMain .ecosystemsContents .ecosysCompaniesWrap .companiesContent ul{

   width: 605px;
   height: 280px;
   background-color:transparent;
   border-radius: 16px;
   margin: 0px 30px;
}


#ecosystemsAlliances .ecosystemsMain .ecosystemsContents .ecosysCompaniesWrap .companiesContent ul li{

  padding:30px;
  height:calc(100% - 60px);
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: space-between;
  background-color: transparent;
  border-radius: 16px;
  position: relative;
  
}
#ecosystemsAlliances .ecosystemsMain .ecosystemsContents .ecosysCompaniesWrap .companiesContent ul li .opacityBack{

   width: 100%;
   height: 100%;
   background-color: #1A1A1A;
   border-radius: 16px;
   position: absolute;
   left:0;
   top:0;

}
#ecosystemsAlliances .ecosystemsMain .ecosystemsContents .ecosysCompaniesWrap .companiesContent ul li:hover .opacityBack{

   opacity: 40%;
   filter: contrast(50);
   transition: 500ms;
  
}


#ecosystemsAlliances .ecosystemsMain .ecosystemsContents .ecosysCompaniesWrap .companiesContent ul li .logo{

   height:32px;
   position: absolute;
}

#ecosystemsAlliances .ecosystemsMain .ecosystemsContents .ecosysCompaniesWrap .companiesContent ul li .logo img{

   height:100%;
   object-fit: contain;
   filter: contrast(0);
}

#ecosystemsAlliances .ecosystemsMain .ecosystemsContents .ecosysCompaniesWrap .companiesContent ul li:hover .logo img{
   filter: contrast(100);
}

#ecosystemsAlliances .ecosystemsMain .ecosystemsContents .ecosysCompaniesWrap .companiesContent ul li .texts{

   margin-top:21%;
   position: absolute;

}

#ecosystemsAlliances .ecosystemsMain .ecosystemsContents .ecosysCompaniesWrap .companiesContent ul li .texts p{

   color:#fff;
   font-weight: 600;
   font-size: 20px;
   line-height: 24px;
}

#ecosystemsAlliances .ecosystemsMain .ecosystemsContents .ecosysCompaniesWrap .companiesContent ul li .texts span{

   font-weight: 400;
   font-size: 15px;
   line-height: 24px;
