#ecosystemsAlliances .ecosystemsMain .ecosysScrollWrappers {

   width: 100%;
   margin:0 auto;
   padding:50px 0px 150px;
}

#ecosystemsAlliances .ecosystemsMain .ecosysScrollWrappers .ecoSysAnimationWrap {

   width: 100%;
   height: 220px;
   margin:0 auto;
   overflow: hidden;
   
}

#ecosystemsAlliances .ecosystemsMain .ecosysScrollWrappers .ecoSysAnimationWrap .ecoSysAnimationBoxs{

   width: 100%;
   height: 160px;
   margin:0 auto;
   overflow: hidden;
   display: flex;
   align-items: center;
   justify-content: center;
}
#ecosystemsAlliances .ecosystemsMain .ecosysScrollWrappers .ecoSysAnimationWrap .ecoSysAnimationBoxs .ecosysSliders{

   display: flex;
   align-items: center;
   justify-content: center;
   height: 100%;
   animation: ecosysSliderFlow01 50s linear infinite;

}
#ecosystemsAlliances .ecosystemsMain .ecosysScrollWrappers .ecoSysAnimationWrap .ecoSysAnimationBoxs .ecosysSlidersAddSeconds{

   animation: ecosysSliderFlow02 100s linear infinite;

}

@keyframes ecosysSliderFlow01{
   0% {
     transform: translateX(-100%);
   }
   100% {
     transform: translateX(100%);
   }
 }

 @keyframes ecosysSliderFlow02{
   0% {
     transform: translateX(100%);
   }
   100% {
     transform: translateX(-100%);
   }
 }

#ecosystemsAlliances .ecosystemsMain .ecosysScrollWrappers .ecosysSlidersPaddings{

   padding-top:120px;
}

#ecosystemsAlliances .ecosystemsMain .ecosysScrollWrappers .ecoSysAnimationWrap .ecoSysAnimationBoxs .ecosysSliders .ecocSysSlides{

   display: flex;
   align-items: center;
   justify-content: center;
   border-radius: 10px;
   width:240px;
   height:100%;
   margin-right:12px;
   transition: 0.25s ease;
