.mainPage {
  margin: 0 auto;
  background-color: #101010;
  padding: 0px 0px 0px 100px;
  color: #fff;
}

.mainPage * {
  box-sizing: border-box;
}

.mainPage .mainContents {
  width: 100%;
  height: 100%;
  margin: 0 auto;
  background-color: #101010;
}

.mainPage main {
  width: 100%;
  overflow: hidden;
}

.mainPage section {
  width: 100%;
  display: flex;
  flex-direction: column;
  position: relative;
}

#mainVisualWrap {
  align-items: center;
  width: 100%;
  height: 100vh;
  background-position: center 300px;
  background-repeat: no-repeat;
  background-size: cover;
  justify-content: center;
  background-color: #040303;
  animation: opening 1.5s ease forwards;
  overflow-x: hidden;
}

@keyframes opening {
  100% {
    background-position: center;
  }
}

#mainVisualWrap .visualTitle {
  width: calc(100% - 100px);
  display: flex;
  flex-direction: column;
  align-items: center;
  transition: 0.75s ease 1.2s;
  position: relative;
  padding: 80px 0;
}

#mainVisualWrap .visualTitle svg {
  display: block;
  position: absolute;
}

#mainVisualWrap .visualTitle svg:first-child {
  top: 0;
  left: 300px;
  margin-bottom: 20px;
  stroke-dasharray: 500;
  stroke-dashoffset: -290;
  animation: visual_svg 0.255s linear forwards;
  transition: 1s ease;
}

#mainVisualWrap .visualTitle svg:last-child {
  bottom: 0;
  right: 300px;
  stroke-dasharray: 300;
  stroke-dashoffset: 300;
  animation: visual_svg 0.35s 0.25s linear forwards;
  transition: 1s ease;
}

@keyframes visual_svg {
  100% {
    stroke-dashoffset: 0;
  }
}
#mainVisualWrap.is_current .visualTitle svg:first-child {
  left: 0 !important;
}

#mainVisualWrap.is_current .visualTitle svg:last-child {
  right: 0 !important;
}

#mainVisualWrap .visualTitle span {
  font-family: "Plus Jakarta Sans", sans-serif;
  font-size: 120px;
  font-weight: 500;
  line-height: 1;
  letter-spacing: -0.02em;
  color: #ffffff;
  text-align: center;
  display: block;
  margin-bottom: 50px;
  opacity: 0;
  transition: opacity 0.35s ease 1s;
}

#mainVisualWrap .visualTitle span strong {
  font-weight: 700;
}

#mainVisualWrap .visualTitle p {
  font-size: 18px;
  font-weight: 400;
  line-height: 1.65;
  letter-spacing: -0.02em;
  text-align: center;
  color: #f3f3f3;
  opacity: 0;
  transition: opacity 0.35s ease 1s;
}

#mainVisualWrap.is_current .visualTitle span,
#mainVisualWrap.is_current .visualTitle p {
  opacity: 1;
}

#mainVisualWrap .visualScroll {
  position: absolute;
  bottom: 60px;
  left: 50%;
  transform: translateX(-50%);
  display: flex;
  flex-direction: column;
  align-items: center;
  font-family: "Plus Jakarta Sans", sans-serif;
  font-size: 13px;
  font-weight: 500;
  line-height: 16.38px;
  letter-spacing: -0.02em;
  text-align: center;
  color: #ffffff;
  transition: 0.75s ease 1.2s;
}

#mainVisualWrap .visualScroll img {
  opacity: 0;
  transform: translateY(-5px);
  animation: scrolling 3s 1s ease infinite;
}

#mainVisualWrap .visualScroll img:last-child {
  animation-delay: 1.5s;
}

@keyframes scrolling {
  0% {
    opacity: 0;
    transform: translateY(-5px);
  }
  30% {
    opacity: 1;
    transform: translateY(0);
  }
  60% {
    opacity: 1;
    transform: translateY(0);
  }
  90% {
    opacity: 0;
    transform: translateY(5px);
  }
  100% {
    opacity: 0;
    transform: translateY(5px);
  }
}

