#mainContent9 {
  width: 100%;
  border-radius: 20px 20px 0px 0px;
  background: #18191a;
  padding: 100px 0 80px;
}

#mainContent9 hgroup {
  width: 100%;
  max-width: 1240px;
  margin: 0 auto 60px;
}
#mainContent9 hgroup h2 {
  font-size: 30px;
  font-weight: 500;
  line-height: 1.4;
  text-align: left;
  margin-top: 10px;
}
#mainContent9 hgroup p {
  color: #9064ee;
}

.inquiryBtn {
  opacity: 1 !important;
}

#mainContent9 div {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  padding: 50px 0 100px;
  position: relative;
}

#mainContent9 div strong {
  font-size: 200px;
  font-weight: 600;
  line-height: 1;
  text-align: center;
  color: #2a2c2d;
  transition: 0.25s ease;
  cursor: default;
}
#mainContent9 div strong:hover {
  color: #383a3b;
}

#mainContent9 div img {
  width: 100%;
  max-width: 1660px;
  border-radius: 20px;
  opacity: 0.6;
  object-fit: cover;
}

#mainContent9 div img.mo {
  display: none;
}

#mainContent9 i,
#mainContent9 div svg {
  position: absolute;
}

#mainContent9 i,
#mainContent9 svg {
  display: block;
}

#mainContent9 i {
  background: #303030;
}

#mainContent9 i.horizontal {
  width: 100%;
  height: 2px;
  transform: scaleX(0);
  transform-origin: left;
}
#mainContent9 i.vertical {
  width: 2px;
  height: calc(100% - 40px);
  transform: scaleY(0);
  transform-origin: top;
}

.contact_line1 {
  top: 0;
}
.contact_line2 {
  top: 20px;
}
.contact_line3 {
  bottom: 20px;
}
.contact_line4 {
  bottom: 0;
}
.contact_line5 {
  top: 20px;
  left: 50px;
}
.contact_line6 {
  top: 20px;
  right: 50px;
}

@keyframes contact_iX {
  100% {
    transform: scaleX(1);
  }
}
@keyframes contact_iY {
  100% {
    transform: scaleY(1);
  }
}

.contact_angle1 {
  stroke-dasharray: 600;
  stroke-dashoffset: -290;
  top: 0;
  left: 50px;
}
.contact_angle2 {
  stroke-dasharray: 600;
  stroke-dashoffset: 600;
  top: 0;
  right: 50px;
}
.contact_angle3 {
  stroke-dasharray: 600;
  stroke-dashoffset: -300;
  bottom: 0;
  left: 50px;
}
.contact_angle4 {
  stroke-dasharray: 600;
  stroke-dashoffset: 600;
  bottom: 0;
  right: 50px;
}

@keyframes contact_svg1 {
  100% {
    stroke-dashoffset: 0;
  }
}
@keyframes contact_svg2 {
  100% {
    stroke-dashoffset: 300;
  }
}
@keyframes contact_svg3 {
  100% {
    stroke-dashoffset: 0;
  }
}
@keyframes contact_svg4 {
  100% {
    stroke-dashoffset: 300;
  }
}

#mainContent9.is_current .contact_line1 {
  animation: contact_iX 2s 0s ease forwards;
}
#mainContent9.is_current .contact_line2 {
  animation: contact_iX 2s 0.25s ease forwards;
}
#mainContent9.is_current .contact_line3 {
  animation: contact_iX 2s 0.5s ease forwards;
}
#mainContent9.is_current .contact_line4 {
  animation: contact_iX 2s 0.75s ease forwards;
}
#mainContent9.is_current .contact_line5 {
  animation: contact_iY 1s 0s ease forwards;
}
#mainContent9.is_current .contact_line6 {
  animation: contact_iY 1s 0.25s ease forwards;
}
#mainContent9.is_current .contact_angle1 {
  animation: contact_svg1 0.35s 0s linear forwards;
}
#mainContent9.is_current .contact_angle2 {
  animation: contact_svg2 0.35s 0.55s linear forwards;
}
#mainContent9.is_current .contact_angle3 {
  animation: contact_svg3 0.35s 0.95s linear forwards;
}
#mainContent9.is_current .contact_angle4 {
  animation: contact_svg4 0.35s 1.25s linear forwards;
}

