#newsDetailsWrapper{
    margin:0 auto;
    padding: 100px 0px 0px 100px;
    background-color: #101010;
    color:#fff;
}


#newsDetailsWrapper .dataInfoWrapper{

    padding:80px 290px 150px;
    margin:0 auto;
    background-color: #101010;
    overflow: hidden;

}

#newsDetailsWrapper .dataInfoWrapper .closeBtns{

    display: flex;
    align-items: center;
    justify-content: flex-start;
    width: 180px;
    padding:5px 0px;
    border-bottom:1px solid #fff;
}

#newsDetailsWrapper .dataInfoWrapper .closeBtns:last-child{

   margin-top: 25px;
}

#newsDetailsWrapper .dataInfoWrapper .closeBtns button{

    font-size: 16px;
    font-weight: 600;
    text-align: right;
    text-transform: uppercase;
    color:#fff;
    width: 100%;
    height: 100%;
    display: block;
    background-repeat: no-repeat;
    background-position: left center;
    background-size: 20px;
    cursor: pointer;

}

#newsDetailsWrapper .dataInfoWrapper .dataInfoContents{

    width: 100%;
    padding: 80px 0px 0px;
    margin:0 auto;
    background-color: #101010;
    overflow: hidden;

}

#newsDetailsWrapper .dataInfoWrapper .dataInfoContents .dataInfoTexts {

    width: 100%;
    margin:0 auto;
    background-color: transparent;
    border-bottom: 1px solid #343434;
    padding: 0px 0px 40px;
    margin-bottom: 40px;
}
#newsDetailsWrapper .dataInfoWrapper .dataInfoContents .dataInfoTexts h2{

    color:#fff;
    font-size: 32px;
    font-weight: 500;
    padding:16px 0px;
    word-break: keep-all;

}

#newsDetailsWrapper .dataInfoWrapper .dataInfoContents .dataInfoTexts p{

    font-size: 14px;
    font-weight: 500;
    color:#9064EE;
    float:left;
    text-align: center;
    padding-right:9px;
    position: relative;
}

#newsDetailsWrapper .dataInfoWrapper .dataInfoContents .dataInfoTexts p::after{

    content:"";
    width: 1px;
    height: 10px;
    background-color: #2D2D2D;
    position: absolute;
    right:0;
    top: 20%;
}

#newsDetailsWrapper .dataInfoWrapper .dataInfoContents .dataInfoTexts span{

    color:#666666;
    font-weight: 400;
    font-size:14px;
    padding:0px 9px;
    display:block;
    float:left;
    position: relative;

}

#newsDetailsWrapper .dataInfoWrapper .dataInfoContents .dataInfoTexts span::after{

    content:"";
    width: 1px;
    height: 10px;
    background-color: #2D2D2D;
    position: absolute;
    right:0;
    top: 20%;

}

#newsDetailsWrapper .dataInfoWrapper .dataInfoContents .dataInfoTexts span:last-child::after{
    
    display: none;
}

#newsDetailsWrapper .dataInfoWrapper .lastBtnWrappers{

    width: 100%;
    margin:0 auto;
    margin-top:40px;
    border-top: 1px solid #343434;

}


@media all and (max-width:1500px){

    #newsDetailsWrapper .dataInfoWrapper { padding: 144px 100px 150px; }
}
@media all and (max-width:1024px){

    #newsDetailsWrapper { padding: 56px 0px 100px 0px;} 
}
@media all and (max-width:900px){

    #newsDetailsWrapper .dataInfoWrapper {
        padding: 144px 90px 150px;
    }
}

@media all and (max-width:768px){
    #newsDetailsWrapper .dataInfoWrapper {
        padding: 144px 16px 150px;
    }
    #newsDetailsWrapper .dataInfoWrapper .closeBtns {width: 147px;}
    #newsDetailsWrapper .dataInfoWrapper .closeBtns button { font-size: 13px; background-size: 18px;}
    #newsDetailsWrapper .dataInfoWrapper .dataInfoContents { padding:50px 0px 0px;}
    #newsDetailsWrapper .dataInfoWrapper .dataInfoContents .dataInfoTexts { margin-bottom: 50px;}
    #newsDetailsWrapper .dataInfoWrapper .dataInfoContents .dataInfoTexts h2 {
        font-size: 28px; 
        line-height: 33.6px;
        width:80%;
    }
    

}