#caseDetailsWrapper{
    margin:0 auto;
    padding: 100px 0px 0px 100px;
    background-color: #101010;
}


#caseDetailsWrapper .caseDetailsDataWrapper{

    margin:0 auto;
    background-color: #101010;

}

#caseDetailsWrapper .caseDetailsDataWrapper .caseDetailsIntroBoxies{

    padding:80px 290px 0px;
    margin:0 auto;
    background-color: #101010;

}

#caseDetailsWrapper .caseDetailsDataWrapper .closeBtns{

    display: flex;
    align-items: center;
    justify-content: flex-start;
    width: 167px;
    padding:5px 0px;
    border-bottom:1px solid #fff;
}

#caseDetailsWrapper .caseDetailsDataWrapper .closeBtns:last-child{

   margin-top: 25px;
}

#caseDetailsWrapper .caseDetailsDataWrapper .closeBtns button{

    font-size: 16px;
    font-weight: 600;
    text-align: right;
    text-transform: uppercase;
    color:#fff;
    width: 100%;
    height: 100%;
    display: block;
    background-repeat: no-repeat;
    background-position: left center;
    background-size: 20px;
    cursor: pointer;

}

#caseDetailsWrapper .caseDetailsDataWrapper .caseDetailsIntroBoxies .caseDetailsInfoContents{

    width: 100%;
    padding: 80px 0px 0px;
    margin:0 auto;
    background-color: #101010;

}

#caseDetailsWrapper .caseDetailsDataWrapper .caseDetailsIntroBoxies .caseDetailsInfoContents .caseDetailsInfoTexts {

    width: 100%;
    margin:0 auto;
    background-color: transparent;
}
#caseDetailsWrapper .caseDetailsDataWrapper .caseDetailsIntroBoxies .caseDetailsInfoContents .caseDetailsInfoTexts h2{

    color:#fff;
    font-size: 52px;
    font-weight: 500;
    padding:50px 0px 30px;
    font-family: Pretendard;

}

#caseDetailsWrapper .caseDetailsDataWrapper .caseDetailsIntroBoxies .caseDetailsInfoContents .caseDetailsInfoTexts p{

    height:25px;
    max-width:200px;
    display: flex;
    align-items: flex-start;
    justify-content: flex-start;
    display:inline-block;
}

#caseDetailsWrapper .caseDetailsDataWrapper .caseDetailsIntroBoxies .caseDetailsInfoContents .caseDetailsInfoTexts p img{

   width: 100%;
   height:100%;
   object-fit: contain;
}

#caseDetailsWrapper .caseDetailsDataWrapper .caseDetailsIntroBoxies .caseDetailsInfoContents .caseDetailsInfoTexts .categoriesWrappers{

    width: 100%;
    overflow: hidden;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    flex-wrap:wrap;

}

#caseDetailsWrapper .caseDetailsDataWrapper .caseDetailsIntroBoxies .caseDetailsInfoContents .caseDetailsInfoTexts .categoriesWrappers strong{
    color:#fff;
    font-weight: 400;
    font-size:14px;
    line-height: 16.8px;
    padding:10px 16px 10px 16px;
    border-radius: 30px;
    background-color: #5832C7;
    margin-right:8px;
    margin-bottom:8px;
}
#caseDetailsWrapper .caseDetailsDataWrapper .caseDetailsIntroBoxies .caseDetailsInfoContents .caseDetailsInfoTexts .categoriesWrappers span{

    color:#C6C6C6;
    font-weight: 400;
    font-size:14px;
    line-height: 16.8px;
    padding:10px 16px 10px 16px;
    border-radius: 30px;
    background-color: #1F1F1F;
    margin-right:8px;
    margin-bottom:8px;

}

#caseDetailsWrapper .caseDetailsDataWrapper .caseDetailsIntroBoxies .caseDetailsInfoContents .caseDetailsInfoBoxies{

    width: 100%;
    margin-top:42px;
    padding: 40px 0px 60px;
    border-top:1px solid #343434;
    background-color: #101010;
}

#caseDetailsWrapper .caseDetailsDataWrapper .caseDetailsIntroBoxies .caseDetailsInfoContents .caseDetailsInfoBoxies .introCompany{

     width: 100%;
     margin:0 auto;
     background-color: transparent;
     display: flex;
     align-content: center;
     justify-content: space-between;

}

#caseDetailsWrapper .caseDetailsDataWrapper .caseDetailsIntroBoxies .caseDetailsInfoContents .caseDetailsInfoBoxies .introCompany li{

    background-color: transparent;
    width:45%;
}


#caseDetailsWrapper .caseDetailsDataWrapper .caseDetailsIntroBoxies .caseDetailsInfoContents .caseDetailsInfoBoxies .introCompany .imgBoxs{

    width:50%;
    height:400px;
    background-color: #1A1A1A;
    border-radius: 10px;
    overflow: hidden;
    display: flex;
    align-items: center;
    justify-content: center;

}

#caseDetailsWrapper .caseDetailsDataWrapper .caseDetailsIntroBoxies .caseDetailsInfoContents .caseDetailsInfoBoxies ul .imgBoxs img{

    width: 100%;
    height:100%;
    object-fit: cover;
}

#caseDetailsWrapper .caseDetailsDataWrapper .caseDetailsIntroBoxies .caseDetailsInfoContents .caseDetailsInfoBoxies ul li .textBoxs{

    width: 100%;
    margin:0 auto;
    background-color: #101010;


}

#caseDetailsWrapper .caseDetailsDataWrapper .caseDetailsIntroBoxies .caseDetailsInfoContents .caseDetailsInfoBoxies ul li .textBoxs h3{
    font-weight: 700;
    font-size: 16px;
    line-height: 22px;
    color:#FFFFFF;
    padding-bottom:30px;
    width:100px;
    word-break: keep-all;

}
#caseDetailsWrapper .caseDetailsDataWrapper .caseDetailsIntroBoxies .caseDetailsInfoContents .caseDetailsInfoBoxies ul li .textBoxs textarea{

    background-color: transparent;
    border:none;
    font-size: 18px;
    font-weight: 400;
    color:#737373;
    margin:0 auto;
    display: block;
    width: 100%;
    /* height:326px; */
    min-height:auto;
    font-family: Pretendard; 
    line-height: 32px;
    resize: none;
    word-break: keep-all;
}

#caseDetailsWrapper .caseDetailsDataWrapper .caseDetailsIntroBoxies .caseDetailsInfoContents .caseDetailsInfoBoxies ul li .textBoxs textarea::-webkit-scrollbar{
   appearance: none;
}

#caseDetailsWrapper .caseDetailsDataWrapper .caseDetailsIntroBoxies .caseDetailsInfoContents .caseDetailsInfoBoxies ul li .textBoxs textarea:focus{
    outline: none;
}

#caseDetailsWrapper .caseDetailsDataWrapper .dataStoryBoxies {

    width:95%;
    /* height:965px; */
    height: 100%;
    float:right;
    background-color: #18191A;
    border-radius: 20px 0px 0px 20px;
}

#caseDetailsWrapper .caseDetailsDataWrapper .dataStoryBoxies .storyContent{

    width: calc(100% - 450px);
    height: calc(100% / 3);
    margin:0 auto;
    display: flex;
    align-items: center;
    justify-content: center;
    border-left: 1px solid #343434;
    position: relative;
    cursor: default;
    padding:70px 0px;

}


#caseDetailsWrapper .caseDetailsDataWrapper .dataStoryBoxies .storyContent em{

    position: absolute;
    left:-1px;
    top:40%;
    background-color: #fff;
    height:0px;
    width: 1px;
    opacity: 50%;
    transition: height 800ms;
}


#caseDetailsWrapper .caseDetailsDataWrapper .dataStoryBoxies .storyContent ul{

    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
    opacity: 50%;
    width:22%;
    height:100%;

}
#caseDetailsWrapper .caseDetailsDataWrapper .dataStoryBoxies .storyContent:hover ul{

    opacity: 100%;
}

#caseDetailsWrapper .caseDetailsDataWrapper .dataStoryBoxies .storyContent:hover em{

    opacity: 100%;
    height:50%;
    transition: height 800ms;
}

#caseDetailsWrapper .caseDetailsDataWrapper .dataStoryBoxies .storyContent ul.storyText{
    
    width:72%;
    display: flex;
    align-items: flex-start;
    justify-content: center;
}

#caseDetailsWrapper .caseDetailsDataWrapper .dataStoryBoxies .storyContent ul.storyText li{

    width: 100%;
    padding:40px 0px 0px;
}
#caseDetailsWrapper .caseDetailsDataWrapper .dataStoryBoxies .storyContent ul li h3{

    font-size: 28px;
    font-weight: 600;
    color:#fff;
    padding:5px 0px;
}
#caseDetailsWrapper .caseDetailsDataWrapper .dataStoryBoxies .storyContent ul li p{

    font-size: 17px;
    font-weight: 300;
    color:#DBD9D3;
    line-height: 32px;
    width: 100%;
    margin:0 auto;
    word-break: keep-all;

}

#caseDetailsWrapper .caseDetailsDataWrapper .dataStoryBoxies .storyContent ul li img{

    height:20px;
}

#caseDetailsWrapper .caseDetailsDataWrapper .dataBrandTextsBox{

    padding:80px 290px 150px;
    margin:0 auto;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;

}

#caseDetailsWrapper .caseDetailsDataWrapper .dataBrandTextsBox .brandDataContents{

    width: 100%;
    padding:0px 0px 110px;
    border-bottom: 1px solid #343434;
    

}



#caseDetailsWrapper .caseDetailsDataWrapper .dataBrandTextsBox .brandDataContents .dataBrandEmPhasizeImg{
    width: 100%;
    height: 30px;
    margin:0 auto;
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom:50px;
}


#caseDetailsWrapper .caseDetailsDataWrapper .dataBrandTextsBox .brandDataContents .dataBrandEmPhasizeImg img{
    height:20px;
}

#caseDetailsWrapper .caseDetailsDataWrapper .dataBrandTextsBox .brandDataContents .brandTexts{

    width: 100%;

}

#caseDetailsWrapper .caseDetailsDataWrapper .dataBrandTextsBox .brandDataContents .brandTexts p{

    font-size: 18px;
    font-weight: 300;
    color:#DBD9D3;
    margin:0 auto;
    line-height: 34px;
    text-align: center;
    word-break: keep-all;
    text-align: center;
    width:calc(100% - 170px);

}

#caseDetailsWrapper .caseDetailsDataWrapper .dataBrandTextsBox .brandDataContents .brandTexts h3{

    font-weight: 700;
    font-size: 16px;
    color:#9064EE;
    text-align: center;
    padding:28px 0px 0px;
}


/* 반응형 */


@media all and (max-width:1500px) {
    
#caseDetailsWrapper .caseDetailsDataWrapper .dataStoryBoxies .storyContent{ width: calc(100% - 200px);}
/* #caseDetailsWrapper .caseDetailsDataWrapper .dataBrandTextsBox .brandDataContents .brandTexts p{
    width:calc(100% / 1.5);
} */
}

@media all and (max-width:1300px) {
    
#caseDetailsWrapper .caseDetailsDataWrapper .caseDetailsIntroBoxies { padding:80px 90px 0px;}
#caseDetailsWrapper .caseDetailsDataWrapper .dataBrandTextsBox { padding:80px 90px 150px;}
}

@media all and (max-width:1024px) {

  #caseDetailsWrapper { padding:56px 0px 100px;}
  #caseDetailsWrapper .caseDetailsDataWrapper .caseDetailsIntroBoxies .caseDetailsInfoContents .caseDetailsInfoTexts p{
    height:30px;
  }
}

@media all and (max-width:900px) {

#caseDetailsWrapper .caseDetailsDataWrapper .dataStoryBoxies { 
    width: 100%;
    border-radius: 0px;
    overflow: hidden;
}
#caseDetailsWrapper .caseDetailsDataWrapper .dataStoryBoxies .storyContent { 
    width: calc(100% - 32px);
    flex-direction: column;
    padding: 90px 0px 0px;
}

#caseDetailsWrapper .caseDetailsDataWrapper .dataStoryBoxies .storyContent em { top: 80px; height:100%; opacity: 100%;}
#caseDetailsWrapper .caseDetailsDataWrapper .dataStoryBoxies .storyContent:last-child em{ height:calc(100% - 120px);}
#caseDetailsWrapper .caseDetailsDataWrapper .dataStoryBoxies .storyContent:hover ul{ opacity: 100%;}

#caseDetailsWrapper .caseDetailsDataWrapper .dataStoryBoxies .storyContent:hover em{

    opacity:100%;
    height:100%;
}

#caseDetailsWrapper .caseDetailsDataWrapper .dataStoryBoxies .storyContent:last-child{ padding-bottom: 80px;}
#caseDetailsWrapper .caseDetailsDataWrapper .dataStoryBoxies .storyContent ul {
    width: 90%;
    height:auto;
    flex-direction: row;
    opacity: 100%;
    justify-content: flex-start;
    align-items: center;

    }

#caseDetailsWrapper .caseDetailsDataWrapper .dataStoryBoxies .storyContent ul li h3 { font-size: 20px;}
#caseDetailsWrapper .caseDetailsDataWrapper .dataStoryBoxies .storyContent ul li{ display:flex; align-items: center; justify-content: center; padding-right:10px;}
#caseDetailsWrapper .caseDetailsDataWrapper .dataStoryBoxies .storyContent ul li img { height:25px;}
#caseDetailsWrapper .caseDetailsDataWrapper .dataStoryBoxies .storyContent ul.storyText { 
    /* width: 90%; */
    width: calc(100% - 60px);
}
#caseDetailsWrapper .caseDetailsDataWrapper .dataStoryBoxies .storyContent ul.storyText li { padding:20px 0px 0px;}
#caseDetailsWrapper .caseDetailsDataWrapper .dataBrandTextsBox{ padding:80px 16px 50px;}
#caseDetailsWrapper .caseDetailsDataWrapper .dataBrandTextsBox .brandDataContents .dataBrandImg{ margin-bottom: 30px;}
#caseDetailsWrapper .caseDetailsDataWrapper .dataBrandTextsBox .brandDataContents .brandTexts h3{ font-size: 13px;}
#caseDetailsWrapper .caseDetailsDataWrapper .dataBrandTextsBox .brandDataContents .brandTexts p {font-size: 15px; line-height: 26px;}

}

@media all and (max-width:768px) {

#caseDetailsWrapper .caseDetailsDataWrapper .caseDetailsIntroBoxies { padding:80px 0px 0px; margin:0px 16px; }
#caseDetailsWrapper .caseDetailsDataWrapper .caseDetailsIntroBoxies .caseDetailsInfoContents { padding:50px 0px 0px;}
#caseDetailsWrapper .caseDetailsDataWrapper .closeBtns{ width:136px; }
#caseDetailsWrapper .caseDetailsDataWrapper .closeBtns button{

    font-size: 13px;
    background-size: 18px;
}

#caseDetailsWrapper .caseDetailsDataWrapper .caseDetailsIntroBoxies .caseDetailsInfoContents .caseDetailsInfoTexts h2{
    font-size: 28px;
    padding:30px 0px;
}

#caseDetailsWrapper .caseDetailsDataWrapper .caseDetailsIntroBoxies .caseDetailsInfoContents .caseDetailsInfoTexts .categoriesWrappers span {padding:10px 16px;}
#caseDetailsWrapper .caseDetailsDataWrapper .caseDetailsIntroBoxies .caseDetailsInfoContents .caseDetailsInfoBoxies{
    margin-top:30px;
    padding: 40px 0px 50px;
  }

#caseDetailsWrapper .caseDetailsDataWrapper .caseDetailsIntroBoxies .caseDetailsInfoContents .caseDetailsInfoBoxies .introCompany{display:block;}
#caseDetailsWrapper .caseDetailsDataWrapper .caseDetailsIntroBoxies .caseDetailsInfoContents .caseDetailsInfoBoxies .introCompany li{ width: 100%;}
#caseDetailsWrapper .caseDetailsDataWrapper .caseDetailsIntroBoxies .caseDetailsInfoContents .caseDetailsInfoBoxies ul li .textBoxs h3 { padding-bottom:20px;}
#caseDetailsWrapper .caseDetailsDataWrapper .caseDetailsIntroBoxies .caseDetailsInfoContents .caseDetailsInfoBoxies ul li .textBoxs textarea{ 
    
    font-size: 15px; 
    line-height: 26px; 
    height:auto;
    min-height: auto;
}
#caseDetailsWrapper .caseDetailsDataWrapper .caseDetailsIntroBoxies .caseDetailsInfoContents .caseDetailsInfoBoxies .introCompany .imgBoxs { display:none;}
#caseDetailsWrapper .caseDetailsDataWrapper .dataStoryBoxies .storyContent ul li p{ 
    font-size: 15px;
    line-height: 26px;
}

#caseDetailsWrapper .caseDetailsDataWrapper .dataBrandTextsBox .brandDataContents .brandTexts p {
    width: calc(100% - 30px);
}

#caseDetailsWrapper .caseDetailsDataWrapper .dataBrandTextsBox { padding:50px 16px 50px;}
#caseDetailsWrapper .caseDetailsDataWrapper .dataBrandTextsBox .brandDataContents{

     position: relative;
     padding: 50px 0px 110px;
}
#caseDetailsWrapper .caseDetailsDataWrapper .dataBrandTextsBox .brandDataContents .dataBrandEmPhasizeImg {

  position: absolute;
  top:0px;
  height:100%;
  display:block;
  margin-bottom: 0px;
}

#caseDetailsWrapper .caseDetailsDataWrapper .dataBrandTextsBox .brandDataContents .dataBrandEmPhasizeImg img{

    position: absolute;
    height:16px;
}

#caseDetailsWrapper .caseDetailsDataWrapper .dataBrandTextsBox .brandDataContents .dataBrandEmPhasizeImg img:first-child{

    top:0px;
    left:50%;
}

#caseDetailsWrapper .caseDetailsDataWrapper .dataBrandTextsBox .brandDataContents .dataBrandEmPhasizeImg img:last-child{

    bottom:50px;
    left:50%;
}


}
