.micrositeNav{ 
    height:58px;
    border-radius: 40px; 
    position:fixed; 
    right:20px;
    top:30px;
    display: flex;
    align-items: center;
    justify-content: space-between;
   }

.micrositeNav .microMainNavList{

   width:809px;
   height: 100%;
   border-radius: 40px;
   background-color: #303030;
   box-shadow: 0px 0px 20px 0px rgba(0, 0, 0, 0.059);
   display: flex;
   align-items: center;
   justify-content: center;

}

.micrositeNav .microMainNavList li{

   height:100%;
   padding:0px 15px;
   /* border:1px solid green; */
   position: relative;
}

.micrositeNav .microMainNavList li em{

   position: absolute;
   width: 1px;
   height: 10px;
   right:0px;
   top:42%;
   background-color: rgba(207, 207, 207, 0.302);
}

.micrositeNav .microMainNavList li:last-child em{ display:none;}

.micrositeNav .microMainNavList li a{
 
   color:#fff;
   font-size: 15px;
   font-weight: 400;
   line-height: 18px;
   cursor: pointer;
   /* border: 1px solid red; */
   display: block;
   padding:0 10px;
   height:100%;
   display: flex;
   align-items: center;
   justify-content: center;
}

.micrositeNav .microSubNavList{

   height:100%;
   border-radius: 50%;
   display: flex;
   align-items: center;
   justify-content: center;
   background-color: #303030;
   box-shadow:  0px 0px 20px 0px rgba(0, 0, 0, 0.059);
   margin-left:10px;
}

.micrositeNav .microSubNavList li{

  padding:0px 18px;
  display: flex;
  align-items: center;
  justify-content: center;

}

.micrositeNav .microSubNavList li button{

    background-color: #fff;
    width: 52px;
    height: 28px;
    border-radius: 17px;
    color:#303030;
    font-size: 12px;
    font-weight: 600;
    background-position: 8px center;
    background-repeat: no-repeat;
    background-size: 15px;
    text-align:right;
    padding:0px 10px;
    cursor: pointer;

}
.micrositeNav .microSubNavList li a{

   height:100%;
   display: block;
   width: 100%;
   padding:0px 3px;
   display: flex;
   align-items: center;
   justify-content: center;

}

.micrositeNav .microSubNavList li a img{

   height:20px;
}

.mobileMicroNavList{ display:none; }

@media all and (max-width:1024px) {
   
.micrositeNav {
   width: 100%;
   height:72px;
   top:0px;
   right:0px;
   border-radius: 0px;
   display: block;
   background-color: #fff;

}

.micrositeNav.active{ 
   height:100vh; 
   transition: 500ms;
   background-color:transparent;
}

.micrositeNav .microMainNavList { display:none;}
.micrositeNav .microSubNavList { display:none;}

.mobileMicroNavList{

   width: 100%;
   display:block;

}

.mobileMicroNavList.active{

   background-color: #1E1E1EE5;
   height:100%;
   backdrop-filter: blur(20px);
}

.mobileMicroNavList .moMicroBasicBoxs { 
   width: 100%;
   float:left;
   display: flex;
   align-items: center;
   justify-content: space-between;
}

.mobileMicroNavList .moMicroBasicBoxs .moLogos{
   
   padding-left:20px;
   height:72px;
   display: flex;
   align-items: center;
   justify-content: center;
}

.mobileMicroNavList .moMicroBasicBoxs .moLogos li {
   display: flex;
   align-items: center;
   justify-content: center;
}
.mobileMicroNavList .moMicroBasicBoxs .moLogos li a { height:100%; display: block;}
.mobileMicroNavList .moMicroBasicBoxs .moLogos li img{

   height:50px;
}

.micrositeNav.active .mobileMicroNavList .moMicroBasicBoxs .moLogos li img{ filter: invert(100);}
.mobileMicroNavList .moMicroBasicBoxs .moMenuSubList{

   display: flex;
   align-items: center;
   justify-content: space-around;
   height:72px;
}
.mobileMicroNavList .moMicroBasicBoxs .moMenuSubList li { 
   width: 50%;
   display: flex;
   align-items: center;
   justify-content: center;
}
.mobileMicroNavList .moMenuSubList li button,.mobileMicroNavList .moMenuSubList li a{

   width: 50px;
   height:50px;
   background-position: center center;
   background-repeat: no-repeat;
   background-size: cover;

}

.mobileMicroNavList .moMicroMainNavList{ 

   display: flex;
   flex-direction: column;
   align-items: center;
   height:0px;
   width: 100%;
   overflow-y: hidden;

}

.mobileMicroNavList .moMicroMainNavList.active{

    height:100%;
    overflow-y:unset;

}

.mobileMicroNavList .moMicroMainNavList li{
 
    height:auto;
    width:calc(100% - 40px);
    display: flex;
    align-items: center;
    justify-content: center;
    border-bottom:1px solid #CFCFCF1A;

}
.mobileMicroNavList .moMicroMainNavList li:last-child{

   border-bottom: none;
}
.mobileMicroNavList .moMicroMainNavList li a {

    padding:30px 0px;
    margin:0 auto;
    font-size: 18px;
    font-weight: 400;
    line-height: 21.6px;
    color:#FFFFFF;
    display: block;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;


}

}