*{margin: 0; padding:0;}
body, html{ margin:0; font-family: Pretendard;}
ul li{list-style: none;}
a{text-decoration: none;}
a:visited { color:#fff;}
button { background:none; border: none; color:#fff;}
textarea { 
  background-color: transparent; 
  color:#DBD9D3; 
  border: none; 
  width:100%;
  min-height: 192px; 
  word-break: keep-all;
  resize: none;
}
textarea:focus{ outline: none;}

html{ font-size: 16px; }
body { 
  background-color: #101010;
  color:#fff;
}

body::-webkit-scrollbar-thumb{
  height: 30%;
  border-radius: 10px;
  background: #131313;
}
button { font-family: Pretendard; }
input { font-family: Pretendard;  }
p,a,h1,h2,h3,h4,h5,span{font-family: Pretendard;}
textarea { font-family: Pretendard; }

/* IE의 경우 */
input::-ms-clear,
input::-ms-reveal{
    display:none;
}
/* 크롬의 경우 */
input::-webkit-search-decoration,
input::-webkit-search-cancel-button,
input::-webkit-search-results-button,
input::-webkit-search-results-decoration{
    display:none;
}

.page{ 

   margin:0 auto;
   display: flex;
   flex-direction: column;
   align-items: center;
   justify-content: center;
}

header{

   width: calc(100% - 100px);
   height: auto;
   position: fixed;
   z-index:99999999;
   top:0;
   right:0;
   /* background-color: rgba(0,0,0,.2); */

}

main {

   margin:0 auto;

}

aside {

  width: 100px;
  height:100%;
  position: fixed;
  z-index:99999999;
  left:0;
  top:0;
  background-color: #232323;
}

footer{

  padding:0px 50px 0px 150px;
  margin:0 auto;
}



/* Nav */

.navBar{ 
   width: 100%;
   height: 100px;
   background-color: transparent;
   margin:0 auto;
   display: flex;
   align-items: center;
   justify-content: space-between;
   /* transition: 500ms;  */
   background-color: #000;
   box-sizing: unset;

}
.navBar.active { 

  height:0px; 
  overflow: hidden;
}

/* .navBar.moOpen { height:100vh; } */

.navBar .logoBoxs { 

   display: flex;
   align-items: center;
   justify-content: center;
   height:100px;
   padding-left:50px;

}

.navBar .logoBoxs img{ height:25px; }
.navBar .navLists{

   display: flex;
   flex-direction: row;
   align-items: center;
   justify-content: center;
   height: 100%;
   margin: 0 auto;

}

.navBar .navLists li{

   margin:0px 6px;
   color:#fff; 
   border-radius: 30px;
   display: flex;
   align-items:center;
   justify-content: center;
   
}

.navBar .navLists li a{ 
   color:#BBBBBB;
   font-size: 16px;
   font-weight: 400;
   display: block;
   width: 100%;
   height: 100%;
   padding:10px 20px;
   background-color: transparent;
}

.navBar .navLists li a:hover{ color:#fff;}

.navBar .navLists li a.active{
   color:#fff;
}

/* 셀렉박스 */

.navBar .languageSelects {
  width:70px;
  height: 42px;
  margin-right:30px;
  background-color: transparent;
  position: relative;
  right:10px;
  border-top-left-radius:10px;
  border-top-right-radius:10px;
}

.navBar .languageSelects.active{
  background-color:#191919;
}

.navBar .languageSelects .languageSelectsTitleBtn {
  width: 100%;
  padding: 0px 15px;
  height: 100%;
  position: absolute;
  left: 0;
  display: block;
  background-color: transparent;
  text-align: left;
  font-size: 15px;
  border-radius: 10px;
  color: #fff;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
  background-position: 42px center;
  background-repeat: no-repeat;
  background-size: 15px;
  cursor: pointer;
}

.navBar .languageSelects .languageSelectList {

  position: absolute;
  top: 42px;
  left: 0;
  width: 100%;
  height: 0px;
  border-bottom-left-radius:10px;
  border-bottom-right-radius:10px;
  border: none;
  background-color:transparent;
  transition: 300ms;
  overflow: hidden;
}

.navBar .languageSelects .languageSelectList.active {
  display: block;
  height: 42px;
  z-index: 999999;
  opacity: 1;
  transition: height 500ms;
  background-color:#191919;
}

.navBar .languageSelects .languageSelectList .languageSelectListMember {

  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  margin: 0 auto;
  background-color:transparent;
  overflow: hidden;
}

.navBar .languageSelects .languageSelectList .languageSelectListMember li p {
  filter:contrast(0);
  color:#fff;
  font-size: 15px;
  font-weight: 400;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  padding:12px 15px;
  cursor:pointer;
  background-position: 42px center;
  background-repeat: no-repeat;
  background-size: 15px;
}

.navBar .languageSelects .languageSelectList .languageSelectListMember li p:hover{ 
  filter:none;
}

.navBar .moBtnWrapper { 
  /* position: absolute; */
  position: relative;
  right:12px; 
  top:1.2px;
  width: 50px; 
  height: 50px; 
  display:none;
  
}

.navBar .moBtnWrapper button { 

  width: 100%; 
  height: 100%; 
  display: block;
  background-repeat: no-repeat;
  background-position: center center;
  background-size: 30px;
  cursor: pointer;

}
.navBar .micrositeButtonWrapper { 
  position: absolute;
  top: 100px;
  left:20px;
  width: 157px;
  height: 32px;
  border-bottom-left-radius: 16px;
  border-bottom-right-radius: 16px;
  background-color: #000;
  box-sizing: unset;
  transition: 500ms; 
  display:none;

 }

 .navBar .micrositeButtonWrapper.active{

  display:block;
 }

 .navBar .micrositeButtonWrapper button{

  width: 100%;
  height: 100%;
  padding: 4px 12px 0px 16px;
  display: flex;
  align-items:flex-start;
  justify-content: left;
  color:#fff;
  font-size: 14px;
  font-weight: 600;
  background: transparent;
  background-position: 90% 4px;
  background-repeat: no-repeat;
  background-size: 15px;
  
}


.navBar.active .micrositeButtonWrapper { top:0px; transition: 500ms; }

.moMenus{ display:none; }
