/* Aside */


.asideNav{

  width:calc(100% - 3px);
  margin:7px auto;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #191919;
  border-radius: 16px;

}

.asideNav .asideNavList{

  padding:7px;
  
}

.asideNav .asideNavList li{

   width:100%;
   height:auto;
