@import url('https://fonts.googleapis.com/css2?family=Plus+Jakarta+Sans:ital,wght@0,200..800;1,200..800&display=swap');

#newsEvents{

    margin:0 auto;
    padding: 100px 0px 0px 100px;
    background-color: #101010;
}

#newsEvents .newsEventsMain {

   background-color: #101010;

}

#newsEvents .newsEventsMain .newsEventsTitleboxs{

    width: 100%;
    padding: 180px 0px 50px;
    margin:0 auto;
    overflow: hidden;
}

#newsEvents .newsEventsMain .newsEventsTitleboxs h5{

    font-size: 6.3rem;
    font-weight: 500;
    color:#fff;
    padding:0px 50px;
    font-family: "Plus Jakarta Sans", sans-serif;
    opacity:0;
    transform: translateY(50px);
}

#newsEvents .newsEventsMain .newsEventsTitleboxs h5.active{
    opacity:1;
    transform: translateY(0px);
    transition: 800ms;
}

#newsEvents .newsEventsMain .newsEventContents{

    width:calc(100% - 100px);
    padding:0px 0px 100px;
    margin:0 auto;
}

#newsEvents .newsEventsMain .newsEventContents .categoryBoxies{

    width: 100%;
    margin:0 auto;
    border-top:1px solid #343434;
    position: relative;
    
}

/* categoryBoxies ul 공통 */

#newsEvents .newsEventsMain .newsEventContents .categoryBoxies ul{

    width: 100%;
    padding:20px 0px;
    display: flex;
    align-items: center;
    justify-content: flex-start;
}

#newsEvents .newsEventsMain .newsEventContents .categoryBoxies ul.categorys{

    padding-top:30px;
    padding-left:56px;
}
/**/

#newsEvents .newsEventsMain .newsEventContents .categoryBoxies .filterList{

    padding: 23px;
    border-radius: 50%;
    background-color: #fff;
    background-position: center center;
    background-size: 22px;
    background-repeat: no-repeat;
    margin-right:10px;
    position: absolute;
    left:0px;
    top:30px;
}

/* categoryBoxies ul li 공통 */

#newsEvents .newsEventsMain .newsEventContents .categoryBoxies ul li{

    border-radius: 30px;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-right:10px;
}


#newsEvents .newsEventsMain .newsEventContents .categoryBoxies ul li span{

    color:#C6C6C6;
    padding: 14.5px 25px;
    font-size: 14px;
    font-weight: 500;
    border-radius: 30px;
    background-color: #1e1e1e;
    cursor: pointer;
}

#newsEvents .newsEventsMain .newsEventContents .categoryBoxies ul li span.selected{
    color:#fff;
    background-color:#622DB9;

}
/**/

#newsEvents .newsEventsMain .newsEventContents .categoryBoxies ul li span:hover{

    color:#fff;
    background-color:#622DB9;
}

#newsEvents .newsEventsMain .newsEventContents .categoryBoxies .subCategoryBoxies{

    border-top: 1px solid #343434;
    border-top-style: dashed;
    width: 100%;
}

#newsEvents .newsEventsMain .newsEventContents .categoryBoxies .subCategoryBoxies .subCategory{

    padding-left:56px;
}
#newsEvents .newsEventsMain .newsEventContents .categoryBoxies .subCategoryBoxies .subCategory li span{

    padding: 6.5px 20px;
}

#newsEvents .newsEventsMain .newsEventContents .categoryBoxies .newsEvetResetBtnWrapper{

    position: absolute;
    top:0px;
    right:20px;
    padding:45px 0px 0px;

}

#newsEvents .newsEventsMain .newsEventContents .categoryBoxies .newsEvetResetBtnWrapper button{

    width: 20px;
    height: 20px;
    background-position: center center;
    background-repeat: no-repeat;
    background-size: 20px;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
}

#newsEvents .newsEventsMain .newsEventContents .categoryBoxies .newsEvetResetBtnWrapper button::before{

    content: "필터 초기화";
    position: absolute;
    right:0px;
    width:110px;
    font-size: 13px;
    font-family: Plus Jakarta Sans;
    color:#A1A1A1;
    font-weight: 500;
}

#newsEvents .newsEventsMain .newsEventContents .newsEventResultBoxies {

    width: 100%;
    margin:0 auto;
    padding:60px 0px 50px;
}

#newsEvents .newsEventsMain .newsEventContents .newsEventResultBoxies .resultCount{

   width: 100%;
   display: flex;
   align-items: center;
   justify-content: space-between;
}

#newsEvents .newsEventsMain .newsEventContents .newsEventResultBoxies .resultCount h3{

    font-size: 14px;
    font-weight: 500;
    color:#A9A9A9;
    display: flex;
    align-items: center;
    justify-content: center;

}

#newsEvents .newsEventsMain .newsEventContents .newsEventResultBoxies .resultCount h3 span{

    color:#fff;
    font-weight: 700;
}

#newsEvents .newsEventsMain .newsEventContents .newsEventResultBoxies .resultCount button{

    width: 61px;
    height: 22px;
    text-align: center;
    color:#fff;
    font-size: 14px;
    cursor: pointer;
    text-align: left;
    background-position:40px center;
    background-repeat: no-repeat;
    background-size: 19px;
}

#newsEvents .newsEventsMain .newsEventContents .newsEventResultBoxies .resultCount button.active{
 
    width: 71px;
    background-position:50px center;
}

#newsEvents .newsEventsMain .newsEventContents .newsEventResultBoxies .resultDatas{

    width: 100%;
    margin:0 auto;
    padding-top:30px;
    display: flex;
    align-items: flex-start;
    /* justify-content: space-between; */
    /* justify-content: space-evenly; */
    justify-content: flex-start;
    flex-wrap: wrap;
    gap:80px 30px;

}

#newsEvents .newsEventsMain .newsEventContents .newsEventResultBoxies .resultDatas ul{

    /* width:553px; */
    width: 31.333%;
    /* height:476px; */
    /* margin:0px 10px 60px 0px; */
    padding:0px 0px 10px;
    /* margin:0px 0px 60px 0px; */
    cursor: pointer;
}

#newsEvents .newsEventsMain .newsEventContents .newsEventResultBoxies .resultDatas ul li .imgBoxs{

    width: 100%;
    height: 370px;
    display: flex;
    align-items: center;
    justify-content: center;
    overflow: hidden;
    border-radius: 16px;
    margin-bottom:30px;

}

#newsEvents .newsEventsMain .newsEventContents .newsEventResultBoxies .resultDatas ul li .imgBoxs img{

    width: 100%;
    height:100%;
    object-fit: cover;
    transition:  transform 500ms;
}

#newsEvents .newsEventsMain .newsEventContents .newsEventResultBoxies .resultDatas ul li .imgBoxs:hover img{

    transform: scale(1.1);
    transition: transform 500ms;
}
#newsEvents .newsEventsMain .newsEventContents .newsEventResultBoxies .resultDatas ul li .titleBoxs{

    width: 100%;
    padding:7px 0px 14px;
    display: flex;
    align-items:flex-start;
    justify-content: flex-start;

} 

#newsEvents .newsEventsMain .newsEventContents .newsEventResultBoxies .resultDatas ul li .titleBoxs p{

    color:#9064EE;
    font-size: 14px;
    font-weight: 500;
    text-align: center;
    padding-right:9px;
    float:left;
    position: relative;
    

}

#newsEvents .newsEventsMain .newsEventContents .newsEventResultBoxies .resultDatas ul li .titleBoxs p::after{

    content:"";
    width: 1px;
    height: 10px;
    background-color: #2D2D2D;
    position: absolute;
    right:0;
    top: 20%;
}

#newsEvents .newsEventsMain .newsEventContents .newsEventResultBoxies .resultDatas ul li .titleBoxs span{

    color:#666666;
    font-weight: 400;
    font-size:14px;
    padding:0px 9px;
    display:block;
    float:left;
    position: relative;

}

#newsEvents .newsEventsMain .newsEventContents .newsEventResultBoxies .resultDatas ul li .titleBoxs span::after{

    content:"";
    width: 1px;
    height: 10px;
    background-color: #2D2D2D;
    position: absolute;
    right:0;
    top: 20%;
}

#newsEvents .newsEventsMain .newsEventContents .newsEventResultBoxies .resultDatas ul li .titleBoxs span:last-child::after{
    
    display: none;
}

#newsEvents .newsEventsMain .newsEventContents .newsEventResultBoxies .resultDatas ul li h2{

    color:#fff;
    font-size: 20px;
    font-weight: 500;
    word-break: keep-all;
    /* overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis; */
}


#newsEvents .footerBoxs {
    width: 100%;
    margin:0 auto;
}



/* 반응형 */


@media all and (max-width:1860px) {
    /* #newsEvents .newsEventsMain .newsEventContents .newsEventResultBoxies .resultDatas{

        justify-content: space-evenly;
    } */

}

@media all and (max-width:1500px) {
#newsEvents .newsEventsMain .newsEventContents .newsEventResultBoxies .resultDatas ul {
    width: 48.333%;
}

}
@media all and (max-width:1300px) {
    #newsEvents .newsEventsMain .newsEventContents .newsEventResultBoxies .resultDatas{

        justify-content: center;
    }
    
}

@media all and (max-width:1024px) {

    #newsEvents { padding:56px 0px 16px;}
    #newsEvents > .page > main {width: 100%; }
    #newsEvents .newsEventsMain .newsEventsTitleboxs h5 { text-align: center; font-size: 5rem;}
    #newsEvents .newsEventsMain .newsEventsTitleboxs { padding: 144px 0px 140px;}

    #newsEvents .newsEventsMain .newsEventContents .categoryBoxies{
     
         display: flex;
         flex-direction: column;
         align-items: flex-start;
         justify-content: flex-start;

    }

    #newsEvents .newsEventsMain .newsEventContents .categoryBoxies .filterList {
        position: relative;
        top:20px;

    }
    #newsEvents .newsEventsMain .newsEventContents .categoryBoxies ul{ padding:26px 0px;}
    #newsEvents .newsEventsMain .newsEventContents .categoryBoxies ul.categorys { 
        padding-left:0px;
        padding-top:46px;
    }
    #newsEvents .newsEventsMain .newsEventContents .categoryBoxies .subCategoryBoxies { 
        border-bottom: 1px solid #343434;
    }

    #newsEvents .newsEventsMain .newsEventContents .categoryBoxies .subCategoryBoxies .subCategory{

        padding-left:0px;
    }
    #newsEvents .newsEventsMain .newsEventContents .categoryBoxies .newsEvetResetBtnWrapper { padding:34px 0px 0px;}
    #newsEvents .newsEventsMain .newsEventContents .newsEventResultBoxies .resultDatas ul { 
        width: 100%;
        /* margin: 0px 0px 60px 0px; */
    }

}

@media all and (max-width:900px) {

    #newsEvents .newsEventsMain .newsEventsTitleboxs h5 { font-size: 80px;}
}

@media all and (max-width:768px) {
    #newsEvents .newsEventsMain .newsEventContents { width:calc(100% - 32px);}
    #newsEvents .newsEventsMain .newsEventsTitleboxs h5 { font-size: 50px; padding:0px; }
    #newsEvents .newsEventsMain .newsEventContents .categoryBoxies ul li { margin-right:8px;}
    #newsEvents .newsEventsMain .newsEventContents .categoryBoxies ul li span { padding:14px; white-space: nowrap;}
    #newsEvents .newsEventsMain .newsEventContents .categoryBoxies .subCategoryBoxies .subCategory li span { padding:10px 20px; }
    #newsEvents .newsEventsMain .newsEventContents .newsEventResultBoxies .resultDatas ul { 
        /* height:356px; */
    }
    #newsEvents .newsEventsMain .newsEventContents .newsEventResultBoxies .resultDatas ul li .imgBoxs { 
        height:230px;
        margin-bottom: 20px;
    }
    #newsEvents .newsEventsMain .newsEventContents .newsEventResultBoxies .resultDatas ul li .titleBoxs { 
        display:block;
        padding: 0px 0px 12px;
    }
    #newsEvents .newsEventsMain .newsEventContents .newsEventResultBoxies .resultDatas ul li .titleBoxs p { 
        float:none; 
        text-align: left;
        padding-bottom: 6px;
        font-size: 13px;
    }
    #newsEvents .newsEventsMain .newsEventContents .newsEventResultBoxies .resultDatas ul li .titleBoxs span{
        padding:0px;
        padding-right: 9px;
        display: inline-block;
        float:none;
        font-size: 13px;
    }

    #newsEvents .newsEventsMain .newsEventContents .newsEventResultBoxies .resultDatas ul li .titleBoxs span:last-child{

        padding:0px 9px;
    }
    #newsEvents .newsEventsMain .newsEventContents .newsEventResultBoxies .resultDatas ul li h2{
        line-height: 28px;
        text-overflow:unset;
       }
    

}

@media all and (max-width:375px) {
    #newsEvents .newsEventsMain .newsEventsTitleboxs h5 { font-size: 50px;}
}
