#awsDetailsWrapper{
   margin:0 auto;
   padding: 100px 0px 0px 100px;
   background-color: #101010;
}


#awsDetailsWrapper .awsDetailsDataWrapper{

   margin:0 auto;
   background-color: #101010;

}

#awsDetailsWrapper .awsDetailsDataWrapper .awsDetailsIntroBoxies{

   padding:80px 290px 0px;
   margin:0 auto;
   background-color: #101010;

}

#awsDetailsWrapper .awsDetailsDataWrapper .closeBtns{

   display: flex;
   align-items: center;
   justify-content: flex-start;
   width: 167px;
   padding:5px 0px;
   border-bottom:1px solid #fff;
}

#awsDetailsWrapper .awsDetailsDataWrapper .closeBtns:last-child{

  margin-top: 25px;
}

#awsDetailsWrapper .awsDetailsDataWrapper .closeBtns button{

   font-size: 16px;
   font-weight: 600;
   text-align: right;
   text-transform: uppercase;
   color:#fff;
   width: 100%;
   height: 100%;
   display: block;
   background-repeat: no-repeat;
   background-position: left center;
   background-size: 20px;
   cursor: pointer;

}

#awsDetailsWrapper .awsDetailsDataWrapper .awsDetailsIntroBoxies .awsDetailsInfoContents{

   width: 100%;
   padding: 80px 0px 0px;
   margin:0 auto;
   background-color: #101010;

}

#awsDetailsWrapper .awsDetailsDataWrapper .awsDetailsIntroBoxies .awsDetailsInfoContents .awsDetailsInfoTexts {

   width: 100%;
   margin:0 auto;
   background-color: transparent;
}
#awsDetailsWrapper .awsDetailsDataWrapper .awsDetailsIntroBoxies .awsDetailsInfoContents .awsDetailsInfoTexts h2{

   color:#fff;
   font-size: 52px;
   font-weight: 500;
   line-height: 66px;
   padding:50px 0px 100px;
   font-family: Pretendard;
   word-break: keep-all;
   width:calc(100% / 1.6);

}

#awsDetailsWrapper .awsDetailsDataWrapper .awsDetailsIntroBoxies .awsDetailsInfoContents .awsDetailsInfoTexts p{

   display: flex;
   align-items: center;
   justify-content: left;
   width: 145px;
   height:25px;
}

#awsDetailsWrapper .awsDetailsDataWrapper .awsDetailsIntroBoxies .awsDetailsInfoContents .awsDetailsInfoTexts p img{

  width: 100%;
  height:100%;
  object-fit: contain;
}

#awsDetailsWrapper .awsDetailsDataWrapper .awsDetailsIntroBoxies .awsDetailsInfoContents .awsDetailsInfoBoxies{

   /* width: 100%; */
   margin-top:42px;
   padding: 80px 0px 80px;
   border-top:1px solid #343434;
   border-bottom:1px solid #343434;
   background-color: #101010;

}


#awsDetailsWrapper .awsDetailsDataWrapper .awsDetailsIntroBoxies .awsDetailsInfoContents .awsDetailsInfoBoxies .awsDetailsContentTextBoxs h2{

   font-weight: 600;
   font-size: 30px;
   line-height: 36px;
   color:#FFFFFF;
   padding-bottom:20px;
}

#awsDetailsWrapper .awsDetailsDataWrapper .awsDetailsIntroBoxies .awsDetailsInfoContents .awsDetailsInfoBoxies .awsDetailsContentTextBoxs p{

   font-weight: 400;
   font-size: 17px;
   line-height: 32px;
   color:#737373;
   word-break: keep-all;
   width:calc(100% / 1.85);

}

#awsDetailsWrapper .awsDetailsDataWrapper .awsDetailsIntroBoxies .awsDetailsDevidedCont{

   width: 100%;
   padding:60px 0px 80px;
   margin:0 auto;
   display: flex;
   gap:20px;
}

#awsDetailsWrapper .awsDetailsDataWrapper .awsDetailsIntroBoxies .awsDetailsDevidedCont ul{

   flex:1;
   background-color: #18191A;
   border-radius: 20px;
   padding:50px 50px 166px;
}

#awsDetailsWrapper .awsDetailsDataWrapper .awsDetailsIntroBoxies .awsDetailsDevidedCont ul li h2{

   color:#9064EE;
   font-size: 14px;
   line-height: 22px;
   font-weight: 700;
   padding-bottom: 16px;
   text-transform: uppercase;
}


#awsDetailsWrapper .awsDetailsDataWrapper .awsDetailsIntroBoxies .awsDetailsDevidedCont ul li h4{

   font-weight: 500;
   font-size: 22px;
   color:#fff;
   line-height: 32px;
   word-break: keep-all;
   width:calc(100% / 1.1);
   padding-bottom:30px;
}

#awsDetailsWrapper .awsDetailsDataWrapper .awsDetailsIntroBoxies .awsDetailsDevidedCont ul li p{

   color:#737373;
   font-size: 17px;
   font-weight: 400;
   line-height: 32px;
   word-break: keep-all;
   width:calc(100% / 1.06);
}

#awsDetailsWrapper .awsDetailsDataWrapper .awsDetailsDatas{

    padding:0px 290px 150px;
    margin: 0 auto;
}

#awsDetailsWrapper .awsDetailsDataWrapper .awsDetailsDatas .awsDataCont{

   padding:50px 0px 100px;
   display: flex;
   border-top:1px solid #343434;
   border-bottom:1px solid #343434;
   
}

#awsDetailsWrapper .awsDetailsDataWrapper .awsDetailsDatas .awsDataCont:first-child{
   
   border: none;
}

#awsDetailsWrapper .awsDetailsDataWrapper .awsDetailsDatas .awsDataCont ul{

    display: flex;
    
}

#awsDetailsWrapper .awsDetailsDataWrapper .awsDetailsDatas .awsDataCont .awsDataTitles{

    width:calc(100% / 3);
}

#awsDetailsWrapper .awsDetailsDataWrapper .awsDetailsDatas .awsDataCont .awsDataTitles li{
    
   display: flex;
   align-items: flex-start;
   justify-content: flex-start;
}

#awsDetailsWrapper .awsDetailsDataWrapper .awsDetailsDatas .awsDataCont .awsDataTitles li span{

    color:#585858;
    font-size: 20px;
    font-weight: 400;
    line-height: 22px;
    padding-right:29px;
    padding-top:8px;
}

#awsDetailsWrapper .awsDetailsDataWrapper .awsDetailsDatas .awsDataCont .awsDataTitles li h1{

   font-size: 30px;
   font-weight: 600;
   line-height: 40px;
   text-transform: uppercase;
   width:calc(100% / 1.2);
   word-break: keep-all;
   font-family: Plus Jakarta Sans;
}

#awsDetailsWrapper .awsDetailsDataWrapper .awsDetailsDatas .awsDataCont .awsDataText{

   width:calc(100% - (100% / 3));
}

#awsDetailsWrapper .awsDetailsDataWrapper .awsDetailsDatas .awsDataCont .awsDataText li{

   width: 100%;

}

#awsDetailsWrapper .awsDetailsDataWrapper .awsDetailsDatas .awsDataCont .awsDataText li textarea{

   width: 100%;
   min-height:auto;
   color:#DBD9D3;
   font-size: 17px;
   line-height: 32px;
   font-weight: 300;
   word-break: keep-all;
}

#awsDetailsWrapper .awsDetailsDataWrapper .awsDetailsDatas .awsDataCont .awsDataText li textarea::-webkit-scrollbar{
   appearance: none;
}

@media all and (max-width:1730px){

   #awsDetailsWrapper .awsDetailsDataWrapper .awsDetailsIntroBoxies .awsDetailsInfoContents .awsDetailsInfoTexts h2{

      width:calc(100% / 1.0);
   }
}

@media all and (max-width:1650px){

   #awsDetailsWrapper .awsDetailsDataWrapper { padding: 80px 100px 150px; }
   #awsDetailsWrapper .awsDetailsDataWrapper .awsDetailsIntroBoxies {
      padding: 80px 100px 0px;
   }
   #awsDetailsWrapper .awsDetailsDataWrapper .awsDetailsIntroBoxies .awsDetailsInfoContents .awsDetailsInfoBoxies{

      padding: 80px 0px 50px;
   }

   #awsDetailsWrapper .awsDetailsDataWrapper .awsDetailsIntroBoxies .awsDetailsInfoContents .awsDetailsInfoBoxies .awsDetailsContentTextBoxs p{

      width: 100%;
   }
   #awsDetailsWrapper .awsDetailsDataWrapper .awsDetailsDatas {
      padding: 0px 100px 150px;
   }

#awsDetailsWrapper .awsDetailsDataWrapper .awsDetailsIntroBoxies .awsDetailsDevidedCont ul li h2 { text-align: center;}
   #awsDetailsWrapper .awsDetailsDataWrapper .awsDetailsIntroBoxies .awsDetailsDevidedCont ul li h4  {
      font-size: 20px;
      line-height: 30px;
      width:100%;
      text-align: center;
   }
   #awsDetailsWrapper .awsDetailsDataWrapper .awsDetailsIntroBoxies .awsDetailsDevidedCont ul {padding:30px;}
   #awsDetailsWrapper .awsDetailsDataWrapper .awsDetailsIntroBoxies .awsDetailsDevidedCont ul li p {

      font-size: 16px;
      line-height: 26px;
      text-align: center;
      width:100%;
   }


}@media all and (max-width:1300px){

   #awsDetailsWrapper .awsDetailsDataWrapper { padding: 80px 50px 150px; }
   #awsDetailsWrapper .awsDetailsDataWrapper .awsDetailsIntroBoxies {
      padding: 80px 50px 0px;
   }
   #awsDetailsWrapper .awsDetailsDataWrapper .awsDetailsIntroBoxies .awsDetailsInfoContents .awsDetailsInfoBoxies{

      padding: 80px 50px 50px;
   }

   #awsDetailsWrapper .awsDetailsDataWrapper .awsDetailsIntroBoxies .awsDetailsInfoContents .awsDetailsInfoTexts h2 {

      font-size: 50px;
   }

}

@media all and (max-width:1024px){

   #awsDetailsWrapper { padding: 0px; }
   #awsDetailsWrapper .awsDetailsDataWrapper { padding: 56px 16px 110px; }
   #awsDetailsWrapper .awsDetailsDataWrapper .closeBtns button { font-size: 13px; line-height: 15.6px; background-size: 18px;}
   #awsDetailsWrapper .awsDetailsDataWrapper .awsDetailsIntroBoxies .awsDetailsInfoContents { padding: 30px 0px 0px;}
   #awsDetailsWrapper .awsDetailsDataWrapper .awsDetailsIntroBoxies .awsDetailsInfoContents .awsDetailsInfoBoxies{

      padding: 30px 50px 0px;
      margin-top: 0px;
   }
   #awsDetailsWrapper .awsDetailsDataWrapper .awsDetailsIntroBoxies .awsDetailsInfoContents .awsDetailsInfoTexts h2 {

      font-size:26px;
      line-height: 34px;
      padding: 30px 0px 50px;
   }

   #awsDetailsWrapper .awsDetailsDataWrapper .awsDetailsIntroBoxies .awsDetailsInfoContents .awsDetailsInfoBoxies .awsDetailsContentTextBoxs h2 {

      font-size: 16px;
      line-height: 19.2px;
   }
   #awsDetailsWrapper .awsDetailsDataWrapper .awsDetailsIntroBoxies .awsDetailsInfoContents .awsDetailsInfoBoxies .awsDetailsContentTextBoxs p {

      font-size: 15px;
      line-height: 26px;
   }

   #awsDetailsWrapper .awsDetailsDataWrapper .awsDetailsIntroBoxies .awsDetailsDevidedCont{

      flex-direction: column;
      padding: 50px 0px;
   }
   #awsDetailsWrapper .awsDetailsDataWrapper .awsDetailsDatas {
      padding: 0px 50px 150px;
   }
   #awsDetailsWrapper .awsDetailsDataWrapper .awsDetailsDatas .awsDataCont{ 
      flex-direction: column;
      align-items: center;
      justify-content: center;
      padding: 30px 0px 80px;
   }

   #awsDetailsWrapper .awsDetailsDataWrapper .awsDetailsDatas .awsDataCont .awsDataTitles{

      width:100%;
   }

   #awsDetailsWrapper .awsDetailsDataWrapper .awsDetailsDatas .awsDataCont .awsDataTitles li {

      flex-direction: column;
      align-items: center;
      justify-content: center;
      width: 100%;
   }

   #awsDetailsWrapper .awsDetailsDataWrapper .awsDetailsDatas .awsDataCont .awsDataTitles li h1 {

      text-align: center;
      width:calc(100% / 4);
      font-size: 26px;
      line-height: 31.2px;
   }

   #awsDetailsWrapper .awsDetailsDataWrapper .awsDetailsDatas .awsDataCont .awsDataTitles li span {
      padding:0px;
      padding-bottom: 16px;
      font-size: 14px;
      line-height: 16.8px;
   }

   #awsDetailsWrapper .awsDetailsDataWrapper .awsDetailsDatas .awsDataCont .awsDataText { width:100%; }
   #awsDetailsWrapper .awsDetailsDataWrapper .awsDetailsDatas .awsDataCont .awsDataText li textarea {
      margin-top:30px;
      font-size: 15px;
      line-height: 26px;
      text-align: center;
      min-height:auto;
   }


}

@media all and (max-width:768px){

   #awsDetailsWrapper .awsDetailsDataWrapper .awsDetailsIntroBoxies {
      padding: 80px 16px 0px;
   }
   #awsDetailsWrapper .awsDetailsDataWrapper .awsDetailsIntroBoxies .awsDetailsInfoContents .awsDetailsInfoBoxies{

      padding: 30px 16px 0px;
   }
   #awsDetailsWrapper .awsDetailsDataWrapper .awsDetailsDatas {
      padding: 0px 0px 150px;
   }
   #awsDetailsWrapper .awsDetailsDataWrapper .awsDetailsDatas .awsDataCont .awsDataTitles li h1 {
      width:calc(100% / 3);
   }
}

@media all and (max-width:600px){
#awsDetailsWrapper .awsDetailsDataWrapper .awsDetailsDatas .awsDataCont .awsDataTitles{

   width:100%;
}
#awsDetailsWrapper .awsDetailsDataWrapper .awsDetailsDatas .awsDataCont .awsDataTitles li h1 {
   width:calc(100% / 2.5);
}
}
@media all and (max-width:600px){
   #awsDetailsWrapper .awsDetailsDataWrapper .awsDetailsDatas .awsDataCont .awsDataTitles li h1 {
      width:calc(100% / 2);
   }
}