#techInsightDetailsWrapper{
    margin:0 auto;
    padding: 100px 0px 0px 100px;
    background-color: #101010;
    color:#fff;
}


#techInsightDetailsWrapper .techInsightDataInfoWrapper{

    padding:80px 290px 150px;
    margin:0 auto;
    background-color: #101010;
    overflow: hidden;

}

#techInsightDetailsWrapper .techInsightDataInfoWrapper .closeBtns{

    display: flex;
    align-items: center;
    justify-content: flex-start;
    width: 165px;
    padding:5px 0px;
    border-bottom:1px solid #fff;
}

#techInsightDetailsWrapper .techInsightDataInfoWrapper .closeBtns:last-child{

   margin-top: 25px;
}

#techInsightDetailsWrapper .techInsightDataInfoWrapper .closeBtns button{

    font-size: 16px;
    font-weight: 600;
    text-align: right;
    text-transform: uppercase;
    color:#fff;
    width: 100%;
    height: 100%;
    display: block;
    background-repeat: no-repeat;
    background-position: left center;
    background-size: 20px;
    cursor: pointer;

}

#techInsightDetailsWrapper .techInsightDataInfoWrapper .techInsightDataInfoContents{

    width: 100%;
    padding: 80px 0px 0px;
    margin:0 auto;
    background-color: #101010;
    overflow: hidden;

}

#techInsightDetailsWrapper .techInsightDataInfoWrapper .techInsightDataInfoContents .techInsightDataInfoTexts {

    width: 100%;
    margin:0 auto;
    background-color: transparent;
    border-bottom: 1px solid #343434;
    padding: 0px 0px 40px;
    margin-bottom: 40px;
}

#techInsightDetailsWrapper .techInsightDataInfoWrapper .techInsightDataInfoContents .techInsightDataInfoTexts .techInsightCategoriesWrappers{
    width: 100%;
    overflow: hidden;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    flex-wrap:wrap;

}

#techInsightDetailsWrapper .techInsightDataInfoWrapper .techInsightDataInfoContents .techInsightDataInfoTexts .techInsightCategoriesWrappers span{

    color:#C6C6C6;
    font-weight: 400;
    font-size:14px;
    padding:6px 20px 6px 20px;
    border-radius: 30px;
    background-color: #1F1F1F;
    margin-right:8px;
    margin-bottom:8px;

}

#techInsightDetailsWrapper .techInsightDataInfoWrapper .techInsightDataInfoContents .techInsightDataInfoTexts .techInsightSubInfoBoxs{

    padding:40px 0px 0px;
    width: 100%;
}

#techInsightDetailsWrapper .techInsightDataInfoWrapper .techInsightDataInfoContents .techInsightDataInfoTexts .techInsightSubInfoBoxs ul{

    width: 100%;
    margin:0 auto;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

#techInsightDetailsWrapper .techInsightDataInfoWrapper .techInsightDataInfoContents .techInsightDataInfoTexts .techInsightSubInfoBoxs ul li h3{

    font-weight: 500;
    font-size: 14px;
    color:#fff;
}


#techInsightDetailsWrapper .techInsightDataInfoWrapper .techInsightDataInfoContents .techInsightDataInfoTexts .bigTitle{

    color:#fff;
    font-size: 32px;
    font-weight: 500;
    padding:16px 0px 20px;
    word-break: keep-all;

}

#techInsightDetailsWrapper .techInsightDataInfoWrapper .techInsightDataInfoContents .techInsightDataInfoTexts .techInsightSubInfoBoxs p{

    font-size: 14px;
    font-weight: 500;
    color:#9064EE;
    float:left;
    text-align: center;
    padding-right:9px;
    position: relative;
}

#techInsightDetailsWrapper .techInsightDataInfoWrapper .techInsightDataInfoContents .techInsightDataInfoTexts .techInsightSubInfoBoxs p::after{

    content:"";
    width: 1px;
    height: 10px;
    background-color: #2D2D2D;
    position: absolute;
    right:0;
    top: 20%;
}

#techInsightDetailsWrapper .techInsightDataInfoWrapper .techInsightDataInfoContents .techInsightDataInfoTexts .techInsightSubInfoBoxs span{

    color:#666666;
    font-weight: 400;
    font-size:14px;
    padding:0px 9px;
    display:block;
    float:left;
    position: relative;

}

#techInsightDetailsWrapper .techInsightDataInfoWrapper .techInsightDataInfoContents .techInsightDataInfoTexts .techInsightSubInfoBoxs span::after{

    content:"";
    width: 1px;
    height: 10px;
    background-color: #2D2D2D;
    position: absolute;
    right:0;
    top: 20%;

}

#techInsightDetailsWrapper .techInsightDataInfoWrapper .techInsightDataInfoContents .techInsightDataInfoTexts .techInsightSubInfoBoxs span:last-child::after{
    
    display: none;
}

#techInsightDetailsWrapper .techInsightDataInfoWrapper .techInsightLastBtnWrappers{

    width: 100%;
    margin:0 auto;
    margin-top:40px;
    border-top: 1px solid #343434;

}


@media all and (max-width:1500px){

 #techInsightDetailsWrapper .techInsightDataInfoWrapper { padding: 80px 100px 150px; }
}
@media all and (max-width:1024px){
#techInsightDetailsWrapper { padding: 56px 0px 0px 0px;}
}
@media all and (max-width:900px){

#techInsightDetailsWrapper .techInsightDataInfoWrapper {
        padding: 80px 90px 150px;
    }
}

@media all and (max-width:768px){
    #techInsightDetailsWrapper .techInsightDataInfoWrapper{
        padding: 80px 16px 150px;
    }
    #techInsightDetailsWrapper .techInsightDataInfoWrapper .closeBtns {width: 147px;}
    #techInsightDetailsWrapper .techInsightDataInfoWrapper .closeBtns button { font-size: 13px; background-size: 18px;}
    #techInsightDetailsWrapper .techInsightDataInfoWrapper .techInsightDataInfoContents { padding:50px 0px 0px;}
    #techInsightDetailsWrapper .techInsightDataInfoWrapper .techInsightDataInfoContents .techInsightDataInfoTexts { 
        margin-bottom: 30px;
        padding: 0px 0px 30px;
    }
    #techInsightDetailsWrapper .techInsightDataInfoWrapper .techInsightDataInfoContents .techInsightDataInfoTexts .techInsightCategoriesWrappers span {
        padding:10px 16px 10px 16px;
    }
    #techInsightDetailsWrapper .techInsightDataInfoWrapper .techInsightDataInfoContents .techInsightDataInfoTexts .techInsightSubInfoBoxs{ padding:20px 0px 0px;}
    #techInsightDetailsWrapper .techInsightDataInfoWrapper .techInsightDataInfoContents .techInsightDataInfoTexts .techInsightSubInfoBoxs ul{

        flex-direction: column;
        align-items: flex-start;
    }
    #techInsightDetailsWrapper .techInsightDataInfoWrapper .techInsightDataInfoContents .techInsightDataInfoTexts .techInsightSubInfoBoxs ul li h3 { padding-top:12px;}
    #techInsightDetailsWrapper .techInsightDataInfoWrapper .techInsightDataInfoContents .techInsightDataInfoTexts .bigTitle{
        font-size: 28px; 
        line-height: 33.41px;
        width:80%;
        padding: 16px 0px 30px;
    }
    
    

}