@import url('https://fonts.googleapis.com/css2?family=Plus+Jakarta+Sans:ital,wght@0,200..800;1,200..800&display=swap');

#techInsight{

    margin:0 auto;
    padding: 100px 0px 0px 100px;
    background-color: #101010;
}

#techInsight .techInsightMain {

   background-color: #101010;

}

#techInsight .techInsightMain .techInsightTitleboxs{

    width: 100%;
    padding: 180px 0px 50px;
    margin:0 auto;
}

#techInsight .techInsightMain .techInsightTitleboxs h5{

    font-size: 6.3rem;
    font-weight: 500;
    color:#fff;
    padding:0px 50px;
    font-family: "Plus Jakarta Sans", sans-serif;
    opacity:0;
    transform: translateY(50px);
}

#techInsight .techInsightMain .techInsightTitleboxs h5.active{
    opacity:1;
    transform: translateY(0px);
    transition: 800ms;
}

#techInsight .techInsightMain .techInsightContents{

    width:calc(100% - 100px);
    padding:0px 0px 100px;
    margin:0 auto;
}

#techInsight .techInsightMain .techInsightContents .techInsightCategoryBoxies{

    width: 100%;
    margin:0 auto;
    border-top:1px solid #343434;
    
}

/* categoryBoxies ul 공통 */

#techInsight .techInsightMain .techInsightContents .techInsightCategoryBoxies ul{

    width: 100%;
    display: flex;
    align-items: center;
    justify-content: flex-start;
}

/**/

#techInsight .techInsightMain .techInsightContents .techInsightCategoryBoxies ul.categorys{

    padding:20px 0px;
}

#techInsight .techInsightMain .techInsightContents .techInsightCategoryBoxies ul .filterList{

    padding: 23px;
    border-radius: 50%;
    background-color: #fff;
    background-position: center center;
    background-size: 22px;
    background-repeat: no-repeat;
    margin-right:10px;
}

/* categoryBoxies ul li 공통 */

#techInsight .techInsightMain .techInsightContents .techInsightCategoryBoxies ul li{

    border-radius: 30px;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-right:10px;
}


#techInsight .techInsightMain .techInsightContents .techInsightCategoryBoxies ul li span{

    color:#C6C6C6;
    padding: 14.5px 25px;
    font-size: 14px;
    line-height: 16.71px;
    font-weight: 500;
    border-radius: 30px;
    background-color: #1e1e1e;
    cursor: pointer;
}

#techInsight .techInsightMain .techInsightContents .techInsightCategoryBoxies ul li span.selected{
    color:#fff;
    background-color:#622DB9;

}
/**/

#techInsight .techInsightMain .techInsightContents .techInsightCategoryBoxies ul li span:hover{

    color:#fff;
    background-color:#622DB9;
}


#techInsight .techInsightMain .techInsightContents .techInsightCategoryBoxies .techInsightSubCategoryBoxies{

    padding-left:56px;
    border-top: 1px solid #343434;
    border-top-style: dashed;
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    padding-top: 20px;
    height:100%;
    /* overflow-y: hidden; */
}

#techInsight .techInsightMain .techInsightContents .techInsightCategoryBoxies .techInsightSubCategoryBoxies .techInsightSubCategoryCheckBoxs{

    width: 68%;
    display: flex;
}

#techInsight .techInsightMain .techInsightContents .techInsightCategoryBoxies .techInsightSubCategoryBoxies .techInsightSubCategoryCheckBoxs .checkLists{

    width: 100%;
    height:30px;
    overflow: hidden;
    display: block;

}

#techInsight .techInsightMain .techInsightContents .techInsightCategoryBoxies .techInsightSubCategoryBoxies .techInsightSubCategoryCheckBoxs .checkLists.active{
    height:100%;
    overflow-y:auto;
}

#techInsight .techInsightMain .techInsightContents .techInsightCategoryBoxies .techInsightSubCategoryBoxies .techInsightSubCategoryCheckBoxs .checkLists li{

    padding:0px 0px 12px;
    margin:0 auto;
    display: flex;
    align-items: center;
    justify-content: center;
    float:left;
    margin-right:6.5px;

}


#techInsight .techInsightMain .techInsightContents .techInsightCategoryBoxies .techInsightSubCategoryBoxies .techInsightSubCategoryCheckBoxs .checkLists li span{

    padding:6px 20px 6px 20px;
    background-color: #1F1F1F;
    border-radius: 30px;
    color:#C6C6C6;
    font-size: 14px;
    line-height: 16.71px;
    font-weight: 400;
    display: block;
    text-align: center;
    cursor: pointer;

}

#techInsight .techInsightMain .techInsightContents .techInsightCategoryBoxies .techInsightSubCategoryBoxies .techInsightSubCategoryCheckBoxs ul li span.selected{
    color:#9064EE;
    border:1px solid #9064EE;
    padding:5px 19px 5px 19px;
    background-color: transparent;
     
}

#techInsight .techInsightMain .techInsightContents .techInsightCategoryBoxies .techInsightSubCategoryBoxies .techInsightSubCategoryCheckBoxs .resetBtnBoxs {

    height:100%;
    display: flex;
    align-items: flex-start;
    justify-content: center;
}
#techInsight .techInsightMain .techInsightContents .techInsightCategoryBoxies .techInsightSubCategoryBoxies .techInsightSubCategoryCheckBoxs .resetBtnBoxs .resetBtn{
    background-position: center center;
    background-repeat: no-repeat;
    background-size: 13px;
    width: 20px;
    height: 20px;
    display: block;
    opacity: 50%;
    cursor: pointer;
    margin-top: 5px;
}

#techInsight .techInsightMain .techInsightContents .techInsightCategoryBoxies .moreDatasBtn button{

    color:#C6C6C6;
    font-size: 14px;
    font-weight: 400;
    width: 120px;
    background-position: right center;
    background-repeat: no-repeat;
    background-size: 20px;
    cursor: pointer;

}


#techInsight .techInsightMain .techInsightContents .techInsightResultBoxies {

    width: 100%;
    margin:0 auto;
    padding:106px 0px 50px;
}

#techInsight .techInsightMain .techInsightContents .techInsightResultBoxies .techInsightResultCount{

   width: 100%;
   display: flex;
   align-items: center;
   justify-content: space-between;
}

#techInsight .techInsightMain .techInsightContents .techInsightResultBoxies .techInsightResultCount h3{

    font-size: 14px;
    font-weight: 500;
    color:#A9A9A9;
    display: flex;
    align-items: center;
    justify-content: center;

}

#techInsight .techInsightMain .techInsightContents .techInsightResultBoxies .techInsightResultCount h3 span{

    color:#fff;
    font-weight: 700;
}

#techInsight .techInsightMain .techInsightContents .techInsightResultBoxies .techInsightResultCount button{

    width: 61px;
    height: 22px;
    text-align: center;
    color:#fff;
    font-size: 14px;
    cursor: pointer;
    text-align: left;
    background-position:40px center;
    background-repeat: no-repeat;
    background-size: 19px;
}

#techInsight .techInsightMain .techInsightContents .techInsightResultBoxies .techInsightResultCount button.active{
 
    width: 71px;
    background-position:50px center;
}

#techInsight .techInsightMain .techInsightContents .techInsightResultBoxies .techInsightResultDatas{

    width: 100%;
    margin:0 auto;
    padding-top:30px;

}

#techInsight .techInsightMain .techInsightContents .techInsightResultBoxies .techInsightResultDatas ul{

    width:100%;
    height:426px;
    margin:0px 0px 30px 0px;
    cursor: pointer;
    border-bottom: 1px solid #343434;
}

#techInsight .techInsightMain .techInsightContents .techInsightResultBoxies .techInsightResultDatas ul li{

    display: flex;
    align-items: center;
    justify-content: flex-start;
    width: 100%;
    height: 100%;
}

#techInsight .techInsightMain .techInsightContents .techInsightResultBoxies .techInsightResultDatas ul li .imgBoxs{

    height: 370px;
    /* max-width: 552px; */
    width:32%;
    display: flex;
    align-items: center;
    justify-content: center;
    overflow: hidden;
    border-radius: 16px;
    margin-bottom:30px;

}

#techInsight .techInsightMain .techInsightContents .techInsightResultBoxies .techInsightResultDatas ul li .imgBoxs img{

    width: 100%;
    height: 100%;
    object-fit: cover;
    transition:  transform 500ms;
}

#techInsight .techInsightMain .techInsightContents .techInsightResultBoxies .techInsightResultDatas ul li .imgBoxs:hover img{

    transform: scale(1.1);
    transition: transform 500ms;
}
#techInsight .techInsightMain .techInsightContents .techInsightResultBoxies .techInsightResultDatas ul li .titleBoxs{

    padding:0px 30px;
    width: 60%;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items:flex-start;
    justify-content: space-between;

} 

#techInsight .techInsightMain .techInsightContents .techInsightResultBoxies .techInsightResultDatas ul li .titleBoxs .titleSubBoxs{

    padding: 20px 0px;
    width: 100%;
}

#techInsight .techInsightMain .techInsightContents .techInsightResultBoxies .techInsightResultDatas ul li .titleBoxs .titleSubBoxs .contentsSubInfo{ 
    display: flex; 
}
#techInsight .techInsightMain .techInsightContents .techInsightResultBoxies .techInsightResultDatas ul li .titleBoxs .titleSubBoxs .contentsSubInfo p{

    color:#fff;
    font-size: 14px;
    font-weight: 500;
    padding-right:9px;
    float:left;
    max-width:80px;
    line-height: 16.71px;
    white-space: nowrap;
}

#techInsight .techInsightMain .techInsightContents .techInsightResultBoxies .techInsightResultDatas ul li .titleBoxs .titleSubBoxs .contentsSubInfo .onlyCategories { display: flex; flex-wrap: wrap;}

#techInsight .techInsightMain .techInsightContents .techInsightResultBoxies .techInsightResultDatas ul li .titleBoxs .titleSubBoxs h2{

    color:#fff;
    font-weight: 500;
    font-size: 32px;
    line-height: 38.19px;
    padding:30px 0px 0px;
    word-break: keep-all;
}
#techInsight .techInsightMain .techInsightContents .techInsightResultBoxies .techInsightResultDatas ul li .titleBoxs .titleBigBoxs { padding:40px 0px; display: flex;}
#techInsight .techInsightMain .techInsightContents .techInsightResultBoxies .techInsightResultDatas ul li .titleBoxs .titleBigBoxs p{

    color:#9064EE;
    font-size: 14px;
    line-height: 16.71px;
    font-weight: 500;
    text-align: center;
    padding-right:9px;
    float:left;
    position: relative;
    

}

#techInsight .techInsightMain .techInsightContents .techInsightResultBoxies .techInsightResultDatas ul li .titleBoxs .titleBigBoxs span{

    color:#fff;
    line-height: 16.71px;
}

#techInsight .techInsightMain .techInsightContents .techInsightResultBoxies .techInsightResultDatas ul li .titleBoxs .titleBigBoxs p::after{

    content:"";
    width: 1px;
    height: 10px;
    background-color: #2D2D2D;
    position: absolute;
    right:0;
    top: 20%;
}
#techInsight .techInsightMain .techInsightContents .techInsightResultBoxies .techInsightResultDatas ul li .titleBoxs span{

    color:#666666;
    font-weight: 400;
    font-size:14px;
    line-height: 16.71px;
    padding:0px 9px;
    display:inline-block;
    position: relative;

}


#techInsight .techInsightMain .techInsightContents .techInsightResultBoxies .techInsightResultDatas ul li .titleBoxs .titleSubBoxs span::after{

    content:"";
    width: 1px;
    height: 10px;
    background-color: #2D2D2D;
    position: absolute;
    right:0;
    top: 20%;
}


#techInsight .techInsightMain .techInsightContents .techInsightResultBoxies .techInsightResultDatas ul li .titleBoxs .titleSubBoxs span:last-child::after{
    
    display: none;
}




@media all and (max-width:1860px) {
    #techInsight .techInsightMain .techInsightContents .techInsightResultBoxies .techInsightResultDatas{

        justify-content: space-evenly;
    }

}

