@import url('https://fonts.googleapis.com/css2?family=Plus+Jakarta+Sans:ital,wght@0,200..800;1,200..800&display=swap');

#caseStudies{

    margin:0 auto;
    padding: 100px 0px 0px 100px;
    background-color: #101010;
}

#caseStudies .caseStudiesMain {

   background-color: #101010;
   width:100%;

}

#caseStudies .caseStudiesMain .caseStudiesTitleboxs{
 
     width: calc(100% - 100px);
     padding: 180px 0px 10px;
     margin:0 auto;
 }
 
#caseStudies .caseStudiesMain .caseStudiesTitleboxs h5{
 
     font-size: 8.2rem;
     font-weight: 500;
     color:#fff;
     text-align: center;
     font-family: "Plus Jakarta Sans", sans-serif;
     opacity:0;
     transform: translateY(50px);
 }

 #caseStudies .caseStudiesMain .caseStudiesTitleboxs h5.active{
    opacity:1;
    transform: translateY(0px);
    transition: 800ms;
 }

 #caseStudies .caseStudiesMain .caseStudiesTitleboxs .inputBoxs { 
    position: relative; 
    width: 100%;
    height: 50px;
    padding:54px 0px 0px;
    margin:0 auto;
}
 #caseStudies .caseStudiesMain .caseStudiesTitleboxs .inputBoxs input[type="search"]{

    display: block;
    width:300px;
    padding:10px 30px 10px 10px;
    text-align:left;
    background:none;
    border:none;
    border-bottom:1px solid #B4B4B4;
    color:#B4B4B4;
    position: absolute;
    right:0px;
    font-size: 16px;
    font-family: Pretendard;
 }

 #caseStudies .caseStudiesMain .caseStudiesTitleboxs .inputBoxs input[type="search"]:focus{
    outline: none;
 }

 #caseStudies .caseStudiesMain .caseStudiesTitleboxs .inputBoxs button { 
    width: 30px;
    height: 30px;
    background-position: center center;
    background-repeat: no-repeat;
    background-size: 20px;
    position: absolute;
    right:0px;
    cursor: pointer;

 }
#caseStudies .caseStudiesMain .caseStudiesContents{

    width:calc(100% - 50px);
    padding:0px 0px 100px;
    margin:0 auto;
    border-top:1px solid #343434;
    display: flex;
    align-items: flex-start;
    justify-content: space-between;

}

#caseStudies .caseStudiesMain .caseStudiesContents .categoryBoxies{

    width:19%;
}

#caseStudies .caseStudiesMain .caseStudiesContents .categoryListBoxies{

    width:79%;
    padding:30px 0px;
}

#caseStudies .caseStudiesMain .caseStudiesContents .categoryBoxies .titleBoxs{

    padding: 25px 0px;
    display: flex;
    align-items: center;
    justify-content: right;

}

#caseStudies .caseStudiesMain .caseStudiesContents .categoryBoxies .titleBoxs button{

    background-position: center center;
    background-repeat: no-repeat;
    background-size: 13px;
    display: block;
    opacity: 50%;
    width: 30px;
    height: 30px;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;

}
#caseStudies .caseStudiesMain .caseStudiesContents .categoryBoxies .titleBoxs button.CaseResetBtn{

    opacity: 50%;
    position:relative;

}

#caseStudies .caseStudiesMain .caseStudiesContents .categoryBoxies .titleBoxs button.CaseResetBtn::before{

    content:"필터초기화";
    font-size: 13px;
    color:#A1A1A1;
    display: block;
    position: absolute;
    right:5px;
    width:100px;
    line-height: 16.38px;

}
#caseStudies .caseStudiesMain .caseStudiesContents .categoryBoxies .titleBoxs button.enCaseResetBtn::before{

    content:"Reset";
    width:80px;

}



#caseStudies .caseStudiesMain .caseStudiesContents .categoryBoxies .titleBoxs button.CaseFlipBtn{

    background-size: 20px;
    display: none;
}
#caseStudies .caseStudiesMain .caseStudiesContents .categoryBoxies .titleBoxs button.CaseFlipBtn.active{

    display: block;

}

#caseStudies .caseStudiesMain .caseStudiesContents .categoryBoxies .categoryContents{

    width: 100%;
    margin:0 auto;
}

#caseStudies .caseStudiesMain .caseStudiesContents .categoryBoxies .categoryContents .checkLists{

    margin:0 auto;
    width: 100%;
    height:100%;

}

#caseStudies .caseStudiesMain .caseStudiesContents .categoryBoxies .categoryContents .checkLists.active{
    height:49px;
    overflow: hidden;
}

#caseStudies .caseStudiesMain .caseStudiesContents .categoryBoxies .categoryContents .checkLists li{

    padding:0px 0px 12px;
    margin:0 auto;
    display: flex;
    align-items: center;
    justify-content: center;
    float:left;
    margin-right:8px;

}


#caseStudies .caseStudiesMain .caseStudiesContents .categoryBoxies .categoryContents .checkLists li span{

    padding:10px 16px 10px 16px;
    background-color: #1F1F1F;
    border-radius: 30px;
    color:#C6C6C6;
    font-size: 14px;
    font-weight: 400;
    display: block;
    text-align: center;
    cursor: pointer;

}

#caseStudies .caseStudiesMain .caseStudiesContents .categoryBoxies .categoryContents .checkLists li span.selected{
    color:#9064EE;
    border:1px solid #9064EE;
    padding:9px 15px 9px 15px;
    background-color: transparent;
     
}

#caseStudies .caseStudiesMain .caseStudiesContents .categoryListBoxies .resultCount{

    width: 100%;
    display: flex;
    align-items: flex-start;
    justify-content: flex-start;
}

#caseStudies .caseStudiesMain .caseStudiesContents .categoryListBoxies .resultCount h3{

    font-size: 14px;
    font-weight: 500;
    color:#A9A9A9;
    display: flex;
    align-items: center;
    justify-content: center;

}

#caseStudies .caseStudiesMain .caseStudiesContents .categoryListBoxies .resultCount h3 span{

    color:#fff;
    font-weight: 700;
}

#caseStudies .caseStudiesMain .caseStudiesContents .categoryListBoxies .resultDatas{

    width: 100%;
    margin:0 auto;
    padding-top:50px;
    display: flex;
    align-items: flex-start;
    justify-content: flex-start;
    flex-wrap: wrap;
    gap:70px 50px;

}

#caseStudies .caseStudiesMain .caseStudiesContents .categoryListBoxies .resultDatas ul{

    width:47.9%;
    /* height:596px; */
    padding:0px 0px 10px;
    /* margin:0px 0px 70px 0px; */
    cursor: pointer;
}

#caseStudies .caseStudiesMain .caseStudiesContents .categoryListBoxies .resultDatas ul li .categoriesContent{

    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;

}

#caseStudies .caseStudiesMain .caseStudiesContents .categoryListBoxies .resultDatas ul li .categoriesContent .categoryCont{

    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-start;
    width: 100%;
}

#caseStudies .caseStudiesMain .caseStudiesContents .categoryListBoxies .resultDatas ul li .categoriesContent .categoryCont .bigCategoryWrappers{
    width: 100%;
    padding:0px 0px 10px;
    display: flex;
    align-items: flex-start;
    justify-content: flex-start;
}

#caseStudies .caseStudiesMain .caseStudiesContents .categoryListBoxies .resultDatas ul li .categoriesContent .categoryCont .categoryWrappers{

    height:100%;

}

#caseStudies .caseStudiesMain .caseStudiesContents .categoryListBoxies .resultDatas ul li .categoriesContent .categoryCont .bigCategoryWrappers p{

    font-weight: 600;
    font-size: 14px;
    color:#9064EE;
    margin-left:10px;
    white-space: nowrap;
}

#caseStudies .caseStudiesMain .caseStudiesContents .categoryListBoxies .resultDatas ul li .categoriesContent .categoryCont .bigCategoryWrappers h5{

    font-size: 14px;
    font-weight: 600;
    line-height: 16.8px;
    color:#fff;
}
#caseStudies .caseStudiesMain .caseStudiesContents .categoryListBoxies .resultDatas ul li .categoriesContent .categoryCont .categoryWrappers span{
    
    font-size: 14px;
    font-weight: 400;
    color:#666666;
    padding:0px 9px 5px 0px;
    margin-right:9px;
    display:block;
    float:left;
    position: relative;
}

#caseStudies .caseStudiesMain .caseStudiesContents .categoryListBoxies .resultDatas ul li .categoriesContent .categoryCont .categoryWrappers span::after{
    
    content:"";
    width: 1px;
    height: 10px;
    background-color: #2D2D2D;
    position: absolute;
    right:0;
    top: 15%;
}
#caseStudies .caseStudiesMain .caseStudiesContents .categoryListBoxies .resultDatas ul li .categoriesContent .categoryCont span:last-child::after{
    display:none;
}

#caseStudies .caseStudiesMain .caseStudiesContents .categoryListBoxies .resultDatas ul li .categoriesContent button{
    
    display: block;
    width:40px;
    height:40px;
    margin:0 auto;
    background-position: center center;
    background-repeat: no-repeat;
    background-size: cover;
    cursor: pointer;
    transform: translateY(20px);
    opacity: 0;

}

#caseStudies .caseStudiesMain .caseStudiesContents .categoryListBoxies .resultDatas ul:hover li .categoriesContent button{
    transform: translateY(0px);
    opacity: 1;
    transition: 800ms;
}

#caseStudies .caseStudiesMain .caseStudiesContents .categoryListBoxies .resultDatas ul li .listInfoBoxies{

    /* overflow: hidden;
    height:144px; */
    margin-bottom:30px;
}

#caseStudies .caseStudiesMain .caseStudiesContents .categoryListBoxies .resultDatas ul li .listInfoBoxies h1{

    font-weight: 500;
    color:#fff;
    font-size: 28px;
    line-height: 33.6px;
    padding:34px 0px 12px;
    word-break: keep-all;

}

#caseStudies .caseStudiesMain .caseStudiesContents .categoryListBoxies .resultDatas ul li .listInfoBoxies p{

    font-size: 15px;
    font-weight: 400;
    color:#fff;
    /* width:350px; */
    width:calc(100% / 1.6);
    line-height: 26px;
    word-break: keep-all;

}
#caseStudies .caseStudiesMain .caseStudiesContents .categoryListBoxies .resultDatas ul li .listImgs{

    width: 100%;
    height: 360px;
    display: flex;
    align-items: center;
    justify-content: center;
    overflow: hidden;
    border-radius: 16px;
    background-color:#1A1A1A;
    /* margin-bottom:30px; */

}

#caseStudies .caseStudiesMain .caseStudiesContents .categoryListBoxies .resultDatas ul li .listImgs img{

    width: 100%;
    height: 100%;
    object-fit: cover;
    transition: transform 500ms;
}

#caseStudies .caseStudiesMain .caseStudiesContents .categoryListBoxies .resultDatas ul li .listImgs:hover img{

    transform: scale(1.1);
    transition: transform 500ms;
}

@media all and (max-width:1660px){
    #caseStudies .caseStudiesMain .caseStudiesContents .categoryListBoxies .resultDatas {
        flex-direction: column;
        gap:80px 0px;
    }
#caseStudies .caseStudiesMain .caseStudiesContents .categoryListBoxies .resultDatas ul {
    width: 100%;
}
}

@media all and (max-width:1200px) {
    
#caseStudies .caseStudiesMain .caseStudiesTitleboxs { padding:200px 0px 35px;}
#caseStudies .caseStudiesMain .caseStudiesTitleboxs h5 { font-size: 5rem; }
#caseStudies .caseStudiesMain .caseStudiesTitleboxs .inputBoxs { padding:120px 0px 20px;}
#caseStudies .caseStudiesMain .caseStudiesTitleboxs .inputBoxs input[type="search"] {

    width: 100%;
    height:50px;
    padding:0px;
    border-radius: none!important;
}
#caseStudies .caseStudiesMain .caseStudiesTitleboxs .inputBoxs button{ background-size: 30px; width: 50px; height: 50px;}
#caseStudies .caseStudiesMain .caseStudiesContents { flex-direction: column;}

#caseStudies .caseStudiesMain .caseStudiesContents .categoryBoxies { width: 100%; border-bottom: 1px solid #343434; padding-bottom: 20px;}
#caseStudies .caseStudiesMain .caseStudiesContents .categoryListBoxies {width: 100%;}

/* #caseStudies .caseStudiesMain .caseStudiesContents .categoryListBoxies .resultDatas ul{
     width: 100%;
     margin: 0px 0px 60px 0px;
    } */

#caseStudies .caseStudiesMain .caseStudiesContents .categoryListBoxies .resultDatas ul li .listImgs { height:193px;}

#caseStudies .caseStudiesMain .caseStudiesContents .categoryListBoxies .resultDatas ul li .categoriesContent button{

    display:none;
}

#caseStudies .caseStudiesMain .caseStudiesContents .categoryListBoxies .resultDatas ul li .categoriesContent{  padding-bottom:30px;}

#caseStudies .caseStudiesMain .caseStudiesContents .categoryBoxies .categoryContents .checkLists li span{

    padding:10px 16px 10px 16px;
}
#caseStudies .caseStudiesMain .caseStudiesContents .categoryBoxies .categoryContents .checkLists li span.selected{

    padding:9px 15px 9px 15px;
}

}

@media all and (max-width:1024px) {
    
#caseStudies {
     padding:0px;
}
#caseStudies .caseStudiesMain .caseStudiesContents .categoryListBoxies .resultDatas ul li .listInfoBoxies p { 
    width:auto;
}

#caseStudies .caseStudiesMain .caseStudiesContents .categoryBoxies .categoryContents .checkLists {
    display: flex;
    flex-direction: column;
}
}
@media all and (max-width:768px) {

    #caseStudies .caseStudiesMain .caseStudiesContents{ 
        width:auto;
        margin:0px 16px;
    }
    #caseStudies .caseStudiesMain .caseStudiesTitleboxs{ 

        width:auto;
        margin:0px 16px;
    }
    #caseStudies .caseStudiesMain .caseStudiesTitleboxs h5 { font-size: 3.2rem; }

    #caseStudies .caseStudiesMain .caseStudiesContents .categoryListBoxies .resultDatas ul li .categoriesContent .categoryCont .bigCategoryWrappers{
        padding:0px 0px 0px;
    }
    #caseStudies .caseStudiesMain .caseStudiesContents .categoryListBoxies .resultDatas{
        padding-top: 40px;
        gap:80px 0px;
    }
    #caseStudies .caseStudiesMain .caseStudiesContents .categoryListBoxies .resultDatas ul li .listImgs{ height:211px;}
    #caseStudies .caseStudiesMain .caseStudiesContents .categoryListBoxies .resultDatas ul li .listInfoBoxies { 
        margin-bottom:24px;
    }
    #caseStudies .caseStudiesMain .caseStudiesContents .categoryListBoxies .resultDatas ul li .listInfoBoxies h1{ font-size: 20px; line-height: 24px; padding:0px 0px 10px;}
    #caseStudies .caseStudiesMain .caseStudiesContents .categoryListBoxies .resultDatas ul li .listInfoBoxies p { 
        font-size: 13px; 
        opacity: 80%; 
        line-height: 22px;
    }
    #caseStudies .caseStudiesMain .caseStudiesContents .categoryListBoxies .resultDatas ul li .categoriesContent .categoryCont {

        flex-direction: column;

    }
    #caseStudies .caseStudiesMain .caseStudiesContents .categoryListBoxies .resultDatas ul li .categoriesContent .categoryCont p { padding-bottom: 6px;}
}

@media all and (max-width:370px) {
    #caseStudies .caseStudiesMain .caseStudiesContents .categoryBoxies .categoryContents .checkLists li { margin-right:5px;}
}
